<template>
    <div></div>
</template>

<script type="text/javascript">

import Vue from "vue";
import { directive as onClickaway } from 'vue-clickaway';

export default Vue.extend({
    data: function(){
        return {
            showCuUpload: false,

        }
    },
    components: {

    },
    methods: {
        checkUserLevel () {
            if (this.activeuser.user_type != 'admin' || this.activeuser.user_type != 'client') {
                this.clientid = this.activeuser.clientuser.client_id;
                this.setClientUserFlds(this.activeuser.clientuser);
            } else {
            }
        },
        setClientUserFlds (clientuserdata) {
            this.userinfo.email = clientuserdata.cu_email;
            this.userinfo.forwarding_num = clientuserdata.cu_forwarding_num;
            this.userinfo.mobile_num = clientuserdata.cu_mobile_num;
            this.userinfo.extension = clientuserdata.cu_extension;
            this.userinfo.street = clientuserdata.cu_street;
            this.userinfo.city = clientuserdata.cu_city;
            this.userinfo.state = clientuserdata.cu_state;
            this.userinfo.postal = clientuserdata.cu_postal;
        },
        // Common
        // Drag n drop actions
        determineDragAndDropCapable: function() {
            var div = document.createElement('div');
            return ( ( 'draggable' in div )
                    || ( 'ondragstart' in div && 'ondrop' in div ) )
                    && 'FormData' in window
                    && 'FileReader' in window;
        },
        getImagePreviews: function(){
            let reader = new FileReader();
            reader.addEventListener("load", function(){
                this.imagePreview = reader.result;
            }.bind(this), false);
            if (/\.(jpe?g|png|gif)$/i.test( this.files[0].name )) {
                reader.readAsDataURL( this.files[0] );
            }
        },
        collapseClientUserUpload: function() {
            this.showCuUpload = false;
        },
        selectClientUserProfile: function(file) {
            this.files = file.target.files;
            this.getImagePreviews();
        },
        userDetailsTabbing: function(tab) {
            if(tab == 'details') {
                this.isDetails = true;
                this.isPermission = false;
                this.userDetailsTab.isUserDetails = true;
                this.userDetailsTab.isUserPermission = false;
            } else if(tab == 'perm') {
                this.isDetails = false;
                this.isPermission = true;
                this.userDetailsTab.isUserDetails = false;
                this.userDetailsTab.isUserPermission = true;
            } else {

            }
        },
        streetAddressAutocomplete: function(id) {
            var place, placeComponent;
            var autocomplete = new google.maps.places.Autocomplete((document.getElementById(id)), {types: ['geocode']});
            autocomplete.addListener('place_changed', (e) => {
                place = autocomplete.getPlace();
                placeComponent = place.address_components;

                // Update field value
                placeComponent.forEach( function(v, k) {
                    if(v.types[0] == 'route') {
                        this.clientUserObj.cu_street = v.short_name;
                        this.clientObj.client_street = v.short_name;
                    }
                    if(v.types[0] == 'locality') {
                        this.clientUserObj.cu_city = v.short_name;
                        this.clientObj.client_city = v.short_name;
                    }
                    if(v.types[0] == 'administrative_area_level_1') {
                        this.clientUserObj.cu_state = v.short_name;
                        this.clientObj.client_state = v.short_name;
                    }
                    if(v.types[0] == 'postal_code') {
                        this.clientUserObj.cu_postal = v.short_name;
                        this.clientObj.client_postal = v.short_name;
                    }
                }, this);
                
                // Update client / client user info
                this.updateClientInfo();
                this.updateClientUserInfo();

            });
        },
        togglePasswdVisibility: function() {
            if(this.isNewPassVisible == true) {
                this.isNewPassVisible = false;
            } else {
                this.isNewPassVisible = true;
            }
        },
        // Voicemail actions
        voicemailDropdown: function(val) {
            this.voicemail = val;
            if(val == 'New Recording') {
                $('#voicemailNewRecordingModal').modal('show');
            }
        },
        toggleVoicemail: function(action) {
            if(action == 'play') {
                this.playVoicemail = false;
                this.pauseVoicemail = true;
            } else if(action == 'pause') {
                this.playVoicemail = true;
                this.pauseVoicemail = false;
            } else {
            }
        },
        newVoicemailRecording: function() {
            this.isNewVoicemail = false;
            this.isVoicemailRecording = true;
        },
        stopVoicemailRecording: function() {
            this.isVoicemailRecording = false;
            this.isVoicemailPlayback = true;
        },
        voicemailCancel: function() {
            this.isNewVoicemail = true;
            this.isVoicemailRecording = false;
            this.isVoicemailPlayback = false;
        },
        // Client user modal actions
        changeClientUserPassModal: function() {
            $('#changeClientUserPassModal').modal('show');
        },
        verifyClientUserChangePass: function() {
            clearTimeout(this.timeout);

            var self = this;
            this.timeout = setTimeout( function() {
                if(self.clientUserPass.password == self.clientUserPass.password_confirmation) {
                    self.isPassMatch = true;
                    self.isPassMatchErr = false;
                } else {
                    self.isPassMatch = false;
                    self.isPassMatchErr = true;
                }
            });
        },
        // Client User DB Actions
        updateClientUserInfo: function() {
            clearTimeout(this.timeout);
            var self = this;
            this.timeout = setTimeout( function() {
                axios({
                    method: 'post',
                    url: self.utilsObj.baseclientuserurl+'/ajax/update/'+ self.clientUserObj.id,
                    data: self.clientUserObj,
                })
                .then( response => {
                    // self.getLoggedinUser();
                }).catch( (err) => {
                    // console.log(err.response);
                    // if(err.response.data.message != null) {
                    //     this.addUsersErrModal(err.response.data.message);
                    //     this.errs = err.response.data.errors;
                    // }
                });
            }, 2000);
        },
        updateClientUserImage: function() {
            var dataString = new FormData();
            // Check drag n drop image upload
            if(this.files.length) {
                dataString.append('cupic', this.files[0]);
            }

            axios({
                method: 'post',
                url: this.utilsObj.baseclientuserurl+'/ajax/update/'+this.clientUserObj.id,
                data: dataString,
            })
            .then( response => {
                // this.getLoggedinUser();
            }).catch( (err) => {
                // console.log(err)
            });
        },
        changeClientUserPass: function() {
            axios({
                method: 'post',
                url: this.utilsObj.clientbaseurl+'/user/change_password',
                data: this.clientUserPass,
            })
            .then( response => {
                // this.getLoggedinUser();
                $('#changeClientUserPassModal').modal('hide');
            }).catch( (err) => {
                // console.log(err.response);
            });
        },
        // DB Actions
        addUserToGroup: function(id, groupId) {
            var dataString = new Object();
            dataString['cug_id'] = groupId;
            dataString['clientuser_id'] = id;
            axios({
                method: 'get',
                url: baseCugmemberUrl+'/ajax/store',
                params: dataString,
            })
            .then( response => {

            }).catch( (err) => {
                // console.log(err)
            });
        },
    },
    created() {
    },
    computed: {

    },
    watch: {

    },
    mounted() {
        this.checkUserLevel();
    },
    directives: {
        onClickaway: onClickaway,
    },
    props: {

    },
})

</script>
<style lang="scss">

</style>