<template>
    <div class="sxn-interchange" :class="{expand:isIcContent}">
        <div class="col-maincontent" v-show="current_view != 'nav'">
            <show-lines v-if="current_view == 'lines-and-keywords'"/>
            <chat-widget v-if="current_view == 'chat-widget'"/>
            <add-new-line v-if="current_view == 'add-new-line'"/>
        </div>

        <div class="col-nav" v-if="current_view == 'nav'">
            <ul>
                <li :class="(isLinesKeywordsContent) ? 'active' : null" v-on:click="current_view = 'lines-and-keywords'">
                    <span class="icon _Set_LinesKeywords">
                        <span class="path1"></span><span class="path2"></span><span class="path3"></span><span class="path4"></span><span class="path5"></span>
                    </span> Lines &amp; Keywords
                </li>
                <li :class="(isChatWidgetContent) ? 'active' : null" v-on:click="current_view = 'chat-widget'">
                    <span class="icon _Inter_Chat"></span> Chat Widget
                </li>
            </ul>
        </div>

        <!-- Turn off chat widget notification -->
        <div class="modal modal-notif" tabindex="-1" role="dialog" id="turnOffChatWidgetModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="hideTurnOffChatWidgetModal"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="text-center">
                            <h3>Turn Off Chat Widget</h3>
                            <p>This will remove the widget from your website. <br/> You can turn this back on at any time.</p>
                        </div>
                        <div class="btn-action">
                            <button type="button" class="btn btn-primary">Turn Off</button>
                            <button type="button" class="btn" data-dismiss="modal" v-on:click="hideTurnOffChatWidgetModal">Cancel</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import $ from 'jquery';
import Vue from 'vue'
import Switches from 'vue-switches'
import { directive as onClickaway } from 'vue-clickaway';
import AddNewLine from '@/components/navbar/rightmenu/subs/interchange/AddNewLine.vue'
import ShowLines from '@/components/navbar/rightmenu/subs/interchange/ShowLines.vue'
import ChatWidget from '@/components/navbar/rightmenu/subs/interchange/ChatWidget.vue'

export default Vue.extend({
    components: {
        'add-new-line': AddNewLine,
        'show-lines': ShowLines,
        'chat-widget': ChatWidget,
        'switches': Switches
    },
    props: {

    },
    watch: {
        'rightSubMenuObj.isInterchangeExpand' (newVal) {
            if (newVal) {

            } else {

            }
        }
    },
    data() {
        return {
            current_view: "nav",
            isIcNav: true,
            isIcContent: false,
            isLinesKeywordsContent: false,
            isChatWidgetContent: false,
            isAddNewLineContent: false,
            selectedCheckbox: ['off_chat_widget'],
            customCheckbox: {
                trans_vm_phonelines: 0,
                off_chat_widget: 1
            },
            isShutoffTime: false,
            isCwDirect: true,
            isCwCategory: false,
            cw_option: 'direct',
            cw: {
                initialGreeting: 'How can I help you?',
                btnOne: 'Unassigned',
                btnTwo: 'Unassigned',
                btnThree: 'Unassigned',
                btnFour: 'Unassigned',
                btnOneDisplayName: '',
                btnTwoDisplayName: '',
                btnThreeDisplayName: '',
                btnFourDisplayName: '',
            },
            cwd: { },
            isSearchResults: true,
            // Lines & keywords
            phoneNumbers: null,
            transcribeVoicemails: null


        }
    },
    directives: {
        onClickaway: onClickaway,
    },
    methods: {
        interchangeTab: function(view) {

        },
        chatWidgetOpt: function(opt) {
            if(opt == 'direct') {
                this.isCwDirect = true;
                this.isCwCategory = false;

            } else if(opt == 'category') {
                this.isCwDirect = false;
                this.isCwCategory = true;
            }
        },
        customDropdown: function(dropdown, val) {
            this.cw[dropdown] = val;
        },
        chatWidgetCancel: function() {
            this.isIcContent = false;
        },
        copyToClipboard: function(chatWidgetId) {
            var copyText = document.getElementById(chatWidgetId);
            copyText.select();
            document.execCommand('copy');
            console.log(copyText.value);
        },
        showTurnOffChatWidgetModal: function() {
            $('#turnOffChatWidgetModal').modal('show');
            this.$emit('modalBackdrop', true);
        },
        hideTurnOffChatWidgetModal: function() {
            this.$emit('modalBackdrop', false);
        },
        toggleShutoffTime: function() {
            if(this.isShutoffTime) {
                this.isShutoffTime = false;
            } else {
                this.isShutoffTime = true;
            }
        },
        hideShutoffTimePopover: function() {
            this.isShutoffTime = false;
        }
    }
})
</script>

