<script>
import Vue from 'vue'
import axios from 'axios'
import { Helpers } from '@/helpers'

export default Vue.extend({
    props: {

    },
    data() {
        return {
            activeSettings: null,
            activeAccount: 'isListAccounts',
            selectedPref: [],
            pref: {
                pref_notif: "",
                pref_darkmode: "",
                pref_allowriiqo: "",
                sync_cust_stripe: ""
            },
            client: [],
            commonUrl: Helpers.getEnv('CRM_BASE_URL') + '/api/common',
            baseUrl: Helpers.getEnv('VUE_APP_CRM_BASE_URL'),
            clientid: this.$store.state.rightmenu.clientid,
        };
    },
    created() {
    },
    methods: {
        toggleSettings: function(view) {
            this.activeSettings = view;
            this.$emit('setActiveRightMenu', view);
        },
        toggleAccounts: function(view) {
            this.activeAccount = view;
        },
        togglePref: function(pref) {
            if (this.selectedPref.includes(pref)) {
                this.selectedPref.splice( this.selectedPref.findIndex(v => v === pref), 1 );
                this.togglePrefVal(pref, 0);
            } else {
                this.selectedPref.push(pref);
                this.togglePrefVal(pref, 1);
            }
        },
        togglePrefVal: function(pref, val) {
            if (pref == "pref_notif") {
                return (this.pref.pref_notif = val);
            } else if (pref == "pref_darkmode") {
                return (this.pref.pref_notif = val);
            } else if (pref == "pref_allowriiqo") {
                axios
                .get(
                `${this.commonUrl}/settings/allowriiqo/` +
                this.clientid +
                `/` +
                val
                )
                .then(function () {
                return (this.pref.pref_allowriiqo = val);
                });

                return (this.pref.pref_allowriiqo = val);
            } else if (pref == "sync_cust_stripe") {
                axios
                .get(
                    `${this.commonUrl}/settings/sync_customers/` +
                    this.clientid +
                    `/` +
                    `stripe/` +
                    val
                )
                .then(function () {
                    return (this.pref.sync_cust_stripe = val);
                });

                return (this.pref.sync_cust_stripe = val);
            }
        }
    }
});
</script>

