<template>
    <div>
        <form action="" method="POST" enctype="multipart/form-data" id="updateClientInfo"
            v-on:submit.prevent="updateClientInfo()">
            <!-- Top content -->
            <div class="sca__name">
                <div class="col-profile" v-on-clickaway="collapseClientImageUpload">
                    <!-- Default image container -->
                    <template v-if="userinfo">
                        <div class="profile profile--edit"
                            :style="imageUrl"
                            v-show="!imagePreview" 
                            v-on:click="showClUpload ^= true">
                            <span class="_Inter_Image" v-show="!userinfo.user"></span>
                            <div class="user-level"></div>
                        </div>
                    </template>
                    <!-- End default image container -->
                    <!-- Image preview -->
                    <template v-if="imagePreview">
                        <div class="profile profile--edit"
                            :style="{ backgroundImage: `url('${imagePreview}')` }" 
                            v-show="imagePreview"
                            v-on:click="showClUpload ^= true">
                            <div class="user-level"></div>
                        </div>
                    </template>
                    <!-- End image preview -->
                    <!-- Image upload container -->
                    <div ref="clientUpload" class="profile-upload" 
                        :class="{ open: showClUpload }">
                        <div class="drop-file">
                            <span class="icon _Inter_Social_Storage"></span> <br/>
                            <span class="drop-file-name">DROP FILE TO UPLOAD</span>
                            <div v-for="(f, i) in files" :key="'fu'+i">
                            {{ f.name }}
                            </div>
                        </div>
                        <div class="btn btn-primary btn--uploadpic">
                            Or Browse Files
                            <input type="file" name="cupic" class="form-control-file" @change="selectClientProfile($event)">
                        </div>
                    </div>
                    <!-- End image upload container -->
                </div>
                <div class="col-name">
                    <input type="text" class="input-fname" name="client_first_name" placeholder="User"
                        v-model="userinfo.client_first_name"
                        v-on:keyup="updateClientInfo"><br/>
                    <input type="text" class="input-lname" name="client_last_name" placeholder="Name"
                        v-model="userinfo.client_last_name"
                        v-on:keyup="updateClientInfo">
                    <div class="form-group">
                        <div class="user-type">
                            <span class="user-type__label">User Type:</span> {{ activeuser.user_type }}
                        </div>
                        <div class="user-title">{{ userinfo.client_title }}</div>
                    </div>
                </div>
                <div class="col-addgroup">
                    <div class="dropdown dropdown-addToAGroup">
                        <button class="btn dropdown-toggle" type="button" id="clientUserAddToGroup" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span class="icon-add _Set_Add"></span> <br/>Add To a Group
                        </button>
                        <div class="dropdown-menu" aria-labelledby="clientUserAddToGroup">
                            <a class="dropdown-item" href="#"
                                v-for="(cg, i) in clientGroups" :key="'cg'+i"> <span class="grp-initial" :style="{ backgroundColor: cg.cug_flat_color }">SA</span> {{ cg.cug_name }}</a>
                        </div>
                    </div>
                </div>
            </div>
            <!-- Middle content -->
            <div class="sca__content">
                <h3 class="title">Details</h3>

                <div class="sca__details">
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_email">Email (Username)</label>
                            <input type="email" class="input-text" name="cu_email" placeholder="name@example.com" v-model="userinfo.email">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cupasswd">Password</label>
                            <input type="password" class="input-text" placeholder="******" v-on:click="changeClientPassModal">
                        </div>
                    </div>
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_forwarding_num">Forwarding #</label>
                            <input type="text" class="input-text" name="client_phone_number" value="" placeholder="(000)000-0000" v-mask="'(###)###-####'"
                                v-model="userinfo.forwarding_num"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_mobile_num">Cell #</label>
                            <input type="text" class="input-text phone-field" name="client_mobile_num" placeholder="(000)000-0000" v-mask="'(###)###-####'"
                                v-model="userinfo.mobile_num"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_extension">Extension</label>
                            <input type="text" class="input-text" name="client_extension" placeholder="000" v-mask="'###'"
                                v-model="userinfo.extension"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_extension">Voicemail
                                <span class="_Set_Play" v-show="playVoicemail" v-on:click="toggleVoicemail('play')"></span>
                                <span class="_Set_Pause" v-show="pauseVoicemail" v-on:click="toggleVoicemail('pause')"></span>
                            </label>
                            <div class="dropdown show">
                                <a class="btn dropdown-toggle" href="#" role="button" id="dropdownVoiceMail" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    {{ voicemail }}
                                </a>
                                <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                                    <label>Voicemail</label>
                                    <a class="dropdown-item no-top-padding" href="#" v-on:click="voicemailDropdown('Custom')">Custom</a>
                                    <a class="dropdown-item" href="#" v-on:click="voicemailDropdown('Default')">Default</a>
                                    <a class="dropdown-item" href="#" v-on:click="voicemailDropdown('New Recording')"><span class="_Set_Add"></span> New Recording</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_street">Street Address</label>
                            <input type="text" class="input-text" id="cl_street_address" name="client_street" placeholder="mailing address"
                                v-model="userinfo.street"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_city">City</label>
                            <input type="text" class="input-text" id="cl_locality" name="client_city" placeholder="city"
                                v-model="userinfo.city"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="rm_edit_cu_administrative_area_level_1">State/Province</label>
                            <input type="text" class="input-text" id="cl_administrative_area_level_1" name="client_state" placeholder="state"
                                v-model="userinfo.state"
                                @change="updateClientInfo">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_postal">Zip Code</label>
                            <input type="text" class="input-text" id="cl_cu_postal_code" name="client_postal" placeholder="Zip Code"
                                v-model="userinfo.postal"
                                @change="updateClientInfo">
                        </div>
                    </div>
                </div>
            </div>
            <!-- statistics -->
            <div class="sca__stats">

            </div>
        </form>

        <!-- Change client password modals -->
        <div class="modal modal-notif" tabindex="-1" role="dialog" id="changeClientPassModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"
                            @click="hideModalBackdrop"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="text-center">
                            <span class="modal-icon _Set_UsernamePassword"></span>
                            <h3>Change Password</h3>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="form-group">
                                    <label for="cupasswd">Password <span class="_Inter_Hide"></span></label>
                                    <input type="password" class="input-text" value="*********">
                                </div>
                            </div>
                            <div class="col">
                                <div class="form-group">
                                    <label for="password">New Password <span :class="(isNewPassVisible) ? '_Inter_Show' : '_Inter_Hide'" v-on:click="togglePasswdVisibility"></span></label>
                                    <input :type="(isNewPassVisible) ? 'text' : 'password'" class="input-text" name="password" placeholder="New Password" 
                                    v-model="clientPass.password">
                                </div>
                                <div class="form-group">
                                    <label for="password_confirmation">Verify New Password</label>
                                    <div class="form-row">
                                        <div class="col-10">
                                            <input :type="(isNewPassVisible) ? 'text' : 'password'" class="input-text" name="password_confirmation" placeholder="Verify Password" 
                                            v-model="clientPass.password_confirmation"
                                            v-on:keyup="verifyClientChangePass">
                                        </div>
                                        <div class="col-2">
                                            <span class="icon-error _Inter_Close_Outlined" v-show="isPassMatchErr"></span>
                                            <span class="icon-check _Inter_CompleteCircle_Outlined" v-show="isPassMatch"></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="btn-action">
                            <button type="button" class="btn btn-primary" v-on:click="changeClientPass" v-show="isPassMatch">Update</button>
                            <button type="button" class="btn" data-dismiss="modal">Cancel</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Voicemail: New Recording modals -->
        <div class="modal modal-notif" tabindex="-1" role="dialog" id="voicemailNewRecordingModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="voicemailCancel"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="text-center">
                            <h3>Record New Voicemail Greeting</h3>
                            <div class="icon-record-wrap" v-on:click="newVoicemailRecording" v-show="isNewVoicemail"><span class="icon-record _Inter_Microphone_Outlined"></span></div>
                            <div class="icon-record-wrap stop" v-show="isVoicemailRecording" v-on:click="stopVoicemailRecording"><span class="icon-stop _Cust_Square"></span></div>
                            <div class="icon-record-wrap playback" v-show="isVoicemailPlayback">
                                <span class="icon-play _Inter_Play_Filled"></span>
                                <span class="refresh _Inter_Refresh"></span>
                            </div>
                            <p v-show="isNewVoicemail">Click to Start New Recording</p>
                            <p v-show="isVoicemailRecording">Click to Stop Recording</p>
                            <p class="voicemail-length" v-show="isVoicemailPlayback">00:24</p>
                        </div>
                        <div class="row">
                            
                        </div>
                        <div class="btn-action">
                            <button type="button" class="btn btn-primary">Save</button>
                            <button type="button" class="btn" data-dismiss="modal" v-on:click="voicemailCancel">Cancel</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/javascript">

import Vue from "vue";
import axios from 'axios';
import $ from 'jquery';
import { directive as onClickaway } from 'vue-clickaway';
import { VueMaskDirective } from 'v-mask';
import { Helpers } from '@/helpers';

export default Vue.extend({
    data: function(){
        return {
            crmBaseUrl: Helpers.getEnv("CRM_BASE_URL"),
            clienturl: Helpers.getEnv("CRM_BASE_URL") + '/api/client',
            clientid: null,
            userinfo: {
                client_first_name: null,
                client_last_name: null,
                client_title: null,
                email: null,
                forwarding_num: null,
                mobile_num: null,
                extension: null,
                street: null,
                city: null,
                state: null,
                postal: null,
                user: {}
            },
            // Image upload
            files: [],
            showClUpload: false,
            imagePreview: false,
            // Change pass property
            isNewPassVisible: false,
            isPassMatchErr: false,
            isPassMatch: false,
            clientPass: {
                client_id: '',
                password: '',
                password_confirmation: ''
            },
            // Voicemail data property
            voicemail: 'Custom',
            playVoicemail: true,
            pauseVoicemail: false,
            isNewVoicemail: true,
            isVoicemailRecording: false,
            isVoicemailPlayback: false,
            // Groups
            clientGroups: []
        }
    },
    components: {

    },
    methods: {
        checkUserLevel () {
            if (this.activeuser.user_type == 'admin') {
                this.clientid = this.activeuser.activeuser.client_id;
                this.clientPass.client_id = this.clientid;
                this.getClientInfo(this.clientid);
            } else if (this.activeuser.user_type == 'client') {
                this.clientid = this.activeuser.client.id;
                this.clientPass.client_id = this.clientid;
                this.setClientFlds(this.activeuser.client);
            } else {
            }
        },
        getClientInfo (clientid) {
            this.clientid = clientid;
            axios.get(`${this.clienturl}/${clientid}`)
            .then(response => {
                this.setClientFlds(response.data);
                console.log(response.data);
            })
        },
        setClientFlds (clientdata) {
            this.userinfo.client_first_name = clientdata.client_first_name;
            this.userinfo.client_last_name = clientdata.client_last_name;
            this.userinfo.client_title = clientdata.client_title;
            this.userinfo.email = clientdata.client_email;
            this.userinfo.forwarding_num = clientdata.client_forwarding_num;
            this.userinfo.mobile_num = clientdata.client_mobile_num;
            this.userinfo.extension = clientdata.client_extension;
            this.userinfo.street = clientdata.client_street;
            this.userinfo.city = clientdata.client_city;
            this.userinfo.state = clientdata.client_state;
            this.userinfo.postal = clientdata.client_postal;
        },
        // Common
        // Drag n drop actions
        determineDragAndDropCapable: function() {
            var div = document.createElement('div');
            return ( ( 'draggable' in div )
                    || ( 'ondragstart' in div && 'ondrop' in div ) )
                    && 'FormData' in window
                    && 'FileReader' in window;
        },
        getImagePreviews: function(){
            let reader = new FileReader();
            reader.addEventListener("load", function(){
                this.imagePreview = reader.result;
            }.bind(this), false);
            if (/\.(jpe?g|png|gif)$/i.test( this.files[0].name )) {
                reader.readAsDataURL( this.files[0] );
            }
        },
        // Client methods
        collapseClientImageUpload: function() {
            this.showClUpload = false;
        },
        // Client user methods
        selectClientProfile: function(file) {
            this.files = file.target.files;
            this.getImagePreviews();

            // Upload the image automatically
            setTimeout( function() {
                this.updateClientImage();
                this.showClUpload = false;
            }.bind(this), 1000);
        },
        streetAddressAutocomplete: function(id) {
            var place, placeComponent;
            var autocomplete = new google.maps.places.Autocomplete((document.getElementById(id)), {types: ['geocode']});
            autocomplete.addListener('place_changed', (e) => {
                place = autocomplete.getPlace();
                placeComponent = place.address_components;

                // Update field value
                placeComponent.forEach( function(v, k) {
                    if(v.types[0] == 'route') {
                        this.clientUserObj.cu_street = v.short_name;
                        this.clientObj.client_street = v.short_name;
                    }
                    if(v.types[0] == 'locality') {
                        this.clientUserObj.cu_city = v.short_name;
                        this.clientObj.client_city = v.short_name;
                    }
                    if(v.types[0] == 'administrative_area_level_1') {
                        this.clientUserObj.cu_state = v.short_name;
                        this.clientObj.client_state = v.short_name;
                    }
                    if(v.types[0] == 'postal_code') {
                        this.clientUserObj.cu_postal = v.short_name;
                        this.clientObj.client_postal = v.short_name;
                    }
                }, this);
                
                // Update client / client user info
                this.updateClientInfo();
                this.updateClientUserInfo();

            });
        },
        // Common modal actions
        togglePasswdVisibility: function() {
            if(this.isNewPassVisible == true) {
                this.isNewPassVisible = false;
            } else {
                this.isNewPassVisible = true;
            }
        },
        changeClientPassModal: function() {
            $('#changeClientPassModal').modal('show');
            this.$emit('modalBackdrop', true);
        },
        hideModalBackdrop: function() {
            this.$emit('modalBackdrop', false);
        },  
        verifyClientChangePass: function() {
            clearTimeout(this.timeout);
            var self = this;
            this.timeout = setTimeout( function() {
                if(self.clientPass.password == self.clientPass.password_confirmation) {
                    self.isPassMatch = true;
                    self.isPassMatchErr = false;
                } else {
                    self.isPassMatch = false;
                    self.isPassMatchErr = true;
                }
            });
        },
        // Voicemail actions
        voicemailDropdown: function(val) {
            this.voicemail = val;
            if(val == 'New Recording') {
                $('#voicemailNewRecordingModal').modal('show');
                this.$emit('modalBackdrop', true);
            }
        },
        toggleVoicemail: function(action) {
            if(action == 'play') {
                this.playVoicemail = false;
                this.pauseVoicemail = true;
            } else if(action == 'pause') {
                this.playVoicemail = true;
                this.pauseVoicemail = false;
            } else {
            }
        },
        newVoicemailRecording: function() {
            this.isNewVoicemail = false;
            this.isVoicemailRecording = true;
        },
        stopVoicemailRecording: function() {
            this.isVoicemailRecording = false;
            this.isVoicemailPlayback = true;
        },
        voicemailCancel: function() {
            this.isNewVoicemail = true;
            this.isVoicemailRecording = false;
            this.isVoicemailPlayback = false;
            this.$emit('modalBackdrop', false);
        },
        // DB Actions
        addUserToGroup: function(id, groupId) {
            var dataString = new Object();
            dataString['cug_id'] = groupId;
            dataString['clientuser_id'] = id;
            axios({
                method: 'get',
                url: this.crmBaseUrl+'/cugmember/ajax/store',
                params: dataString,
            })
            .then( response => {

            }).catch( (err) => {
                
            });
        },
        // Client DB Actions
        updateClientInfo: function() {
            let myObj = new FormData();
            let apiUrl;
            // client save
            if (this.isClient) {
                apiUrl = this.crmBaseUrl + '/api/client/update/' + this.clientid;
                myObj.append('client_forwarding_num', this.userinfo.forwarding_num);
                myObj.append('client_mobile_num', this.userinfo.mobile_num);
                myObj.append('client_extension', this.userinfo.extension);
                myObj.append('client_street', this.userinfo.street);
                myObj.append('client_city', this.userinfo.city);
                myObj.append('client_state', this.userinfo.state);
                myObj.append('client_postal', this.userinfo.postal);
            }

            axios({
                method: 'post',
                url: apiUrl,
                data: myObj
            })
            .then(response => {
                console.log("client updated")
            })
        },
        updateClientImage: function() {
            var dataString = new FormData();
            // Check drag n drop image upload
            if(this.files.length) {
                dataString.append('cupic', this.files[0]);

                // Append address field
                dataString.append('client_street', this.userinfo.client_street);
                dataString.append('client_city', this.userinfo.client_city);
                dataString.append('client_state', this.userinfo.client_state);
                dataString.append('client_postal', this.userinfo.client_postal);
            }
            axios({
                method: 'post',
                url: this.crmBaseUrl+'/ajax/update/'+this.clientid,
                data: dataString,
            })
            .then( response => {
                // this.getLoggedinUser();
                // this.imagePreview = null;
            }).catch( (err) => {
                // console.log(err)
            });
        },
        changeClientPass: function() {
            axios({
                method: 'post',
                url: this.crmBaseUrl+'/user/change_password',
                data: this.clientPass,
            })
            .then( response => {
                $('#changeClientPassModal').modal('hide');
                this.$emit('modalBackdrop', false);
            }).catch( (err) => {
                
            });
        },
    },
    created() {
    },
    computed: {
        imageUrl () {
            let styleObj = new Object;
            if (this.userinfo.user) {
                styleObj['backgroundImage'] = "";
                //styleObj['backgroundImage'] = "url(" + Helpers.getEnv("RIIQO_APP_FILES_URL") + "/" + this.userinfo.user.user_pic + ")"; 
            } else {
                styleObj['backgroundImage'] = ""
            }
            return styleObj
        }
    },
    watch: {

    },
    mounted() {
        this.checkUserLevel();

        // Address auto-complete for client user
        this.streetAddressAutocomplete('cl_street_address');

        // Drag n drop upload
        this.dragAndDropCapable = this.determineDragAndDropCapable();
        if(this.dragAndDropCapable) {
            // Drag listener
            ['drag', 'dragstart', 'dragend', 'dragover', 'dragenter', 'dragleave', 'drop'].forEach( function( evt ) {

                // Check if the client form exist
                this.$refs.clientUpload.addEventListener(evt, function(e){
                    e.preventDefault();
                    e.stopPropagation();
                }.bind(this));

            }.bind(this));

            // Client upload event listener
            this.$refs.clientUpload.addEventListener('drop', function(e){
                this.files = e.dataTransfer.files;
                this.getImagePreviews();
                // console.log(e.dataTransfer.files);

                // Upload the image automatically
                setTimeout( function() {
                    this.updateClientImage();
                    this.showClUpload = false;
                }.bind(this), 1000);
                
            }.bind(this));
        }
    },
    directives: {
        mask: VueMaskDirective,
        onClickaway: onClickaway,
    },
    props: {
        activeuser: Object
    },
})

</script>
<style lang="scss">

</style>