<template>
    <div class="sxn-usrs sxn-client-users show" sxn-type="users" v-show="$store.state.rightmenu.usersTab.isUserList">
        <div class="btn-group btn-group--users" role="group" aria-label="Basic example">
            <button type="button" class="btn tab-nav-btn active" tab-type="users" v-on:click="setUsersTab('isUserList')">Users</button>
            <button type="button" class="btn tab-nav-btn" tab-type="groups" v-on:click="setUsersTab('isGroupList')">Groups</button>
            <button type="button" class="btn tab-nav-btn btn--adduser" id="btn--adduser" tab-type="adduser" v-on:click="setUsersTab('isAddUser')"><span class="_Syst_AddCircle"></span></button>
        </div>
        <ul class="mod-usr-lgnd">
            <li class="mod-usr-lgnd__item"><span class="sa"></span> Super Admin</li>
            <li class="mod-usr-lgnd__item"><span class="ad"></span> Admin</li>
            <li class="mod-usr-lgnd__item"><span class="ba"></span> Basic</li>
        </ul>
        <div class="row tile-grid" id="tile-grid-client-users">
            <a href="#" class="tile-grid__item btn--edituser"
            v-for="(user, i) in clientusers"
            :key="'usr'+i"
            v-on:click="editUser(user.id)">
                <div class="tile-content">
                    <div class="tile-inner">
                        <div class="mod-initials">
                            <div class="mod-initials-pro" :style="{ backgroundImage: `url('${assetUrl +'/'+ user.user.user_pic}')` }">
                            <span class="_Cust_Customers" v-show="!user.user.user_pic"></span>
                            </div>
                        </div>
                        <div v-if="user.cu_level == 'super'">
                            <div class="color-bar" style="background-color: #38ad20;"></div>
                        </div>
                        <div v-else-if="user.cu_level == 'admin'">
                            <div class="color-bar" style="background-color: #fff200;"></div>
                        </div>
                        <div v-else-if="user.cu_level == 'basic'">
                            <div class="color-bar" style="background-color: #f79118;"></div>
                        </div>
                    </div>
                </div>
                <div class="tile-title">
                    <h5 class="card-title">{{ user.cu_fname }} {{ user.cu_lname }}</h5>
                    <span>{{ user.cu_position }}</span>
                </div>
            </a>
        </div>
    </div>
</template>

<script>
import Event from '@/eventBus'

import { Helpers } from '@/helpers'

export default {
    props: {
        clientusers: Array
    },
    data() {
        return {
            assetUrl: Helpers.getEnv('RIIQO_APP_FILES_URL')
        }
    },
    methods: {
        setUsersTab: function(view) {
            this.$store.commit('toggleUsersTab', view);
        },
        editUser: function(id) {
            this.$store.commit('editedUser', id);
            this.setUsersTab('isEditUser');
            Event.$emit("setUserFields", id);
        },
    }
}
</script>