<template>
    <div class="form-group form-group--search" v-on-clickaway="clickAwaySearchForm">
        <label for="search_users">Add Users To Group</label>
        <div class="search-box" :class="{ show: activateSearch }">
            <div class="search-box-input">
                <span class="_Set_Search"></span>
                <input type="text" class="input-text eg_search-users search_users" name="eg_search_users" aria-describedby="" placeholder="Search Users"
                v-model="searchKey"
                v-on:click="showAvailableUsers">
            </div>
            <div class="users-list ag_users-list" v-show="availableUserList">
                <label
                :for="'cuc'+au.id"
                v-for="(au, i) in availableUsers"
                :key="'au'+i"
                >
                    <div class="item">
                        <div class="user-img" :style="{ backgroundImage: `url('${amazonUrl +'/'+ au.user.user_pic}')` }">
                            <div v-if="au.cu_level == 'super'">
                                <div class="user-level" style="background-color: #38ad20;"></div>
                            </div>
                            <div v-else-if="au.cu_level == 'admin'">
                                <div class="user-level" style="background-color: #fff200;"></div>
                            </div>
                            <div v-else-if="au.cu_level == 'basic'">
                                <div class="user-level" style="background-color: #f79118;"></div>
                            </div>
                        </div>
                        <div class="user-cont">
                            <h4 class="user-name">{{ au.cu_fname }} {{ au.cu_lname }}</h4>
                            <span class="user-pos">{{ au.cu_position }}</span>
                        </div>
                        <div class="user-act" v-on:click="addTempUsers(au.id)">
                            <input type="checkbox" class="cu-checkbox cu-checkbox--editgroup" :id="'cuc'+au.id" :value="au.id" v-model="tempUsers">
                        </div>
                    </div>
                </label>
            </div>
        </div>
    </div>
</template>

<script>
import { directive as onClickaway } from 'vue-clickaway'
import axios from 'axios'
import Event from '@/eventBus'

import { Helpers } from '@/helpers'

export default {
    props: {
        cug_id: null
    },
    data () {
        return {
            amazonUrl: Helpers.getEnv('RIIQO_APP_FILES_URL'),
            activateSearch: false,
            availableUserList: false,
            availableUsersObj: [],
            searchKey: '',
            tempUsers: [],
            clientusergroupurl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientusergroup',
        }
    },
    directives: {
        onClickaway: onClickaway,
    },
    methods: {
        addTempUsers: function(id) {
            Event.$emit('clickUsers', id);
        },
        showAvailableUsers: function() {
            this.activateSearch = true;
            this.availableUserList = true;
            this.listAvailableUsers();
        },
        clickAwaySearchForm: function() {
            this.activateSearch = false;
            this.availableUserList = false;
        },
        // DB actions
        listAvailableUsers: function() {
            axios
                .get( `${this.clientusergroupurl}/available/${this.cug_id}`)
                .then( response => {
                    // Clear previous data
                    //if(this.availableUsers) {
                        //this.availableUsersObj.length = 0;
                    //}
                    //this.availableUsersObj = response.data;
                    this.tempUsers.length = 0;
                    this.availableUsersObj = Object.keys(response.data).map( (key) => {
                        return response.data[key];
                    });
                }).catch( (err) => {
                    console.log(err)
                })
        },
    },
    computed: {
        availableUsers () {
            return this.availableUsersObj.filter(au => {
                return au.cu_fname.toLowerCase().includes(this.searchKey.toLowerCase()) || au.cu_lname.toLowerCase().includes(this.searchKey.toLowerCase());
            });
        }
    }
}
</script>