/* eslint-disable */
export module Helpers {

  export function setCookie(name: string, val: string, days=7) {
    // console.log("setCookie",name)
    const date = new Date();
    const value = val;

    // Set it expire in 7 days
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));

    // Set it
    document.cookie = name+"="+value+"; expires="+date.toUTCString()+"; path=/";
  }

  export function getCookie(name: string) {
    const value = "; " + document.cookie;
    const parts = value.split("; " + name + "=");

    if (parts.length == 2) {
      return parts.pop().split(";").shift();
    }
  }

  export function deleteCookie(name: string) {
    const date = new Date();

    // Set it expire in -1 days
    date.setTime(date.getTime() + (-1 * 24 * 60 * 60 * 1000));

    // Set it
    document.cookie = name+"=; expires="+date.toUTCString()+"; path=/";
  }


  var cleanedEnvMap = undefined

  export function vueAppEnv() {

    if (cleanedEnvMap != undefined) {
      return cleanedEnvMap
    }

    let processEnvMap = process.env;

    Object.keys(processEnvMap).forEach(function(key) {
      var newKey = key.replace("VUE_APP_","");
      processEnvMap[newKey] = processEnvMap[key];
      delete processEnvMap[key];
    });

    cleanedEnvMap = processEnvMap

    return processEnvMap
  }

  export function getEnv(envVariableName: string) {
    return vueAppEnv()[envVariableName]
  }

}