<template>
	<div>
		<div class="sxn sxn--listpaymentinfo" v-show="paymentTab.isPaymentList">
			<h3 class="title">
				Payment Info <span class="btn-icon-add _Syst_AddCircle" v-on:click="paymentTabbing('add-payment')"></span>
			</h3>
			<div class="row row--cards-header">
				<div class="col-sm-6 col--cardsname">Card #</div>
				<div class="col-sm-3">EXP. DATE</div>
				<div class="col-sm-3"></div>
			</div>
			<div class="mod-bx" data-card_id=""
			v-for="(cc, i) in creditCardArray" :key="'cc'+i">
				<div class="row row--cards-list">
					<div class="col col-sm-6 col--cardsname">#### #### #### {{cc.last4}}</div>
					<div class="col col-sm-3">{{cc.exp_month}}/{{cc.exp_year}}</div>
					<div class="col col-sm-1">
						<span class="_Inter_Delete btn-delete-card"></span>
					</div>
				</div>
				<div class="mod-bx--expand"></div>
			</div>
		</div>

		<!-- add new card -->
		<div class="sxn sxn--addpaymentinfo" v-show="paymentTab.isAddPayment">
			<h3 class="title">
				<span class="btn-icon-back _Syst_Short_LeftArrow" v-on:click="paymentTabbing('list-payment')"></span> Add New Card
			</h3>
			<div class="mod-cc">
				<div class="mod-cc__logo"></div>
				<template v-if="creditcardObj">
					<div class="row row--card row--isCreditCardObj">
						<div class="col col-cardnum">{{ creditcardObj.cardNum }}</div>
					</div>
					<div class="row row--card row--isCreditCardObj last">
						<div class="col col-cardhold">
							<label>Card Holder</label>
							{{ creditcardObj.cardHolder }}
						</div>
						<div class="col col-exp">
							<label>Exp</label>
							{{ creditcardObj.expiry }}
						</div>
					</div>
				</template>
				<template v-else>
					<div class="row row--card">
						<div class="col col-cardnum">Card Number</div>
					</div>
					<div class="row row--card last">
						<div class="col col-cardhold">Card Holder</div>
						<div class="col col-exp">Exp</div>
					</div>
				</template>
			</div>

			<div id="add-ccard-form" class="add-ccard-form">
				<form id="add_credit_card_form" method="POST"
					v-on:submit.prevent="addNewPayment()">
					<div class="row">
						<div class="col">
							<div class="form-group">
								<label for="addCardNumber">Card Number</label>
								<div id="addCardNumber"></div>
							</div>
						</div>
					</div>

					<div class="row">
						<div class="col-sm-5">
							<div class="form-group">
								<label for="addCardHolder">Card Holder</label>
								<input type="text" class="input-text" id="addCardHolder" placeholder="Name on Card">
							</div>
						</div>
						<div class="col col-sm-4">
							<div class="form-group">
								<label for="addCardExp">Exp. Date</label>
								<div id="addCardExp" class="add-card-exp">00 / 00</div>
							</div>
						</div>
						<div class="col">
							<div class="form-group">
								<label for="addCardCvc">CVC</label>
								<div id="addCardCvc" class="add-card-cvc">000</div>
							</div>
						</div>
					</div>

					<div class="row">
						<div class="col">
							<div class="form-group">
								<label for="addCardZipCode">Zip Code</label>
								<input type="text" class="input-text" id="addCardZipCode" placeholder="Zip Code">
							</div>
						</div>
					</div>

					<div class="row">
						<div class="col d-flex flex-column align-items-center">
							<button type="submit" class="btn btn-primary" name="add_ccard_button" id="add-ccard-button" data-loading-text="Please wait...">Save Card</button>
							<button class="btn" v-on:click="paymentTabbing('list-payment')">Cancel</button>
						</div>
					</div>

					<div class="alert-link error card-errors" role="alert">
						<div class="message"></div>
					</div>

					<input type="hidden" name="client_id" :value="this.clientid">
					<a class="reset" href="#" style="display: none;">Reset</a>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import { Helpers } from '@/helpers'

export default {
	data() {
		return {
			paymentTab: {
				isPaymentList: true,
				isAddPayment: false,
			},
			creditcardObj: {
				cardNum: '9302 3839 5739 1728',
				cardHolder: 'James Allen',
				expiry: '03/23'
			},
			creditCardArray: [],
			ccardbaseurl: Helpers.getEnv("CRM_BASE_URL") + '/api/ccard/ajax_view',
            clientid: this.$store.state.rightmenu.clientid,
        }
    },
    mounted: function() {
        this.getCreditCards();
    },
    methods: {
		paymentTabbing: function(tab) {
			if(tab == 'add-payment') {
				this.paymentTab.isAddPayment = true;
				this.paymentTab.isPaymentList = false;
				this.$store.commit('toggleAddCardExpand', true);
			} else {
				this.paymentTab.isPaymentList = true;
				this.paymentTab.isAddPayment = false;
				this.$store.commit('toggleAddCardExpand', false);
			}
		},
		// DB Actions
		getCreditCards: function() {
			axios
				.get(`${this.ccardbaseurl}/${this.clientid}`)
				.then( response => {
					this.creditCardArray = response.data;
				}).catch( (err) => {
					console.log(err)
				});
		},
		addNewPayment: function() {

		}
	}
}
</script>