<template>
    <div id="rightmenu-panels" class="rightmenu-panels">
        <div class="rsm rsm__item rsm__item--personal">
            <PersonalInfo
                :activeuser='activeuser.user'
                v-on:modalBackdrop='showModalBackdrop'/>    
        </div>
        <div class="rsm rsm__item rsm__item--interchange">
            <InterchangeSettings
                v-on:setActiveRightMenu='setActiveSubMenu'
                v-on:modalBackdrop='showModalBackdrop'/>
        </div>
        <div class="rsm rsm__item rsm__item--paymentinfo">
            <PaymentInfo />
        </div>
        <div class="rsm rsm__item rsm__item--history">
            Transaction History
        </div>
        <div class="rsm rsm__item rsm__item--credits">
            <CreditsPurchase 
                v-on:setActiveRightMenu='setActiveSubMenu'/>
        </div>
        <div id="rsmPurchases" class="rsm rsm__item rsm__item--purchases">
                <purchases></purchases>
        </div>
        <div class="rsm rsm__item rsm__item--users">
            <UsersSection 
                :clientid='clientid' 
                :usersObj='usersObj'
                v-on:modalBackdrop='showModalBackdrop'/>
        </div>
        <div class="rsm rsm__item rsm__item--pref">
            <Preferences
                v-on:setActiveRightMenu='setActiveSubMenu'/>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

import PersonalInfo from './subs/PersonalInfo'
import InterchangeSettings from './subs/InterchangeSettings'
import PaymentInfo from './subs/PaymentInfo'
import CreditsPurchase from './subs/CreditsPurchase'
import Preferences from './subs/Preferences'
import Purchases from './subs/Purchases'
import UsersSection from './subs/UsersSection'
import { Helpers } from '@/helpers'

export default {
    data() {
        return {
            rightSubmenuStatus: [],
            clientUserUrl: Helpers.getEnv("CRM_BASE_URL") + '/api/clientuser',
            usersObj: [],
        }
    },
    methods: {
        setActiveSubMenu: function(myfrm) {
            this.$emit('setActiveRightMenu', myfrm);
        },
        // Modal backdrop
        showModalBackdrop: function(bool) {
            this.$emit('modalBackdrop', bool);
        },
        listUsers () {
            axios.get(`${this.clientUserUrl}/list/${this.clientid}`)
            .then(response => {
                this.usersObj = response.data
            })
        }
    },
    mounted () {
        this.listUsers()
    },
    computed: {

    },
    watch: {

    },
    components: {
        PersonalInfo,
        InterchangeSettings,
        PaymentInfo,
        CreditsPurchase,
        Preferences,
        Purchases,
        UsersSection
    },
    props: {
        showMenu: {
            type: Boolean,
        },
        clientid: String | Number,
        isRightMenu: Boolean,
        activeuser: Object,
        activeSubMenu: String
    }
}
</script>