<template>
    <div class="modal" tabindex="-1" role="dialog" id="editGroupModal">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <form action="" method="post" enctype="multipart/form-data" id="frmEditCUGroup">
                    <div class="modal-header">
                        <h5 class="modal-title">Edit Group</h5>
                        <button type="button" class="close" aria-label="Close" v-on:click="hideModal"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <input type="hidden" name="client_id" :value="clientid">
                        <input type="hidden" name="cug_id" :value="groupDetails.cug_id">
                        <input type="hidden" name="cug_flat_color" :value="groupDetails.cug_flat_color">
                        <input type="hidden" name="cug_gradient_start" :value="groupDetails.cug_gradient_start">
                        <input type="hidden" name="cug_gradient_end" :value="groupDetails.cug_gradient_end">
                        <input type="hidden" name="cug_is_flat" :value="groupDetails.cug_is_flat">
                        <div class="row">
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="cug_name">NAME GROUP</label>
                                    <input type="text" class="input-text" name="cug_name" v-model="groupDetails.cug_name" aria-describedby="emailHelp" placeholder="Client group name">
                                </div>
                                <label class="lbl-group-color">GROUP COLOR</label>
                                <div v-if="isFlatColor == '1'">
                                    <div class="selected-color" :style="{ backgroundColor: groupDetails.cug_flat_color }"></div>
                                </div>
                                <div v-if="isFlatColor == '0'">
                                    <div class="selected-color" :style="{ backgroundImage: `linear-gradient(-90deg, ${ groupDetails.cug_gradient_start }, ${ groupDetails.cug_gradient_end })` }"></div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-8">
                                        <ul class="nav nav-pills nav-pills--colors" id="pills-tab-color" role="tablist">
                                            <li class="nav-item">
                                                <a class="nav-link active" id="pills-flatcolor-tab" data-toggle="pill" href="#edit-pills-flatcolor" role="tab" aria-controls="pills-flatcolor" aria-selected="true" v-on:click="colorTabb('1')">Flat</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" id="pills-gradientcolor-tab" data-toggle="pill" href="#edit-pills-gradientcolor" role="tab" aria-controls="pills-gradientcolor" aria-selected="false" v-on:click="colorTabb('0')">Gradient</a>
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-sm-4 col-hex">
                                        <div class="hex">{{ groupDetails.cug_flat_color }}</div>
                                    </div>
                                </div>
                                <div class="tab-content tab-content--colors" id="pills-color-tabContent">
                                    <div class="tab-pane fade show active" id="edit-pills-flatcolor" role="tabpanel" aria-labelledby="pills-flatcolor-tab">
                                        <label class="custom-checkbox select-random">
                                            <input type="checkbox" name="select_randomly" class="select_randomly" v-on:click="selectRandomColor" v-model="select_rand_color">
                                            <span class="checkmark"></span>
                                            Select Randomly
                                        </label>
                                        <div class="colors">
                                            <div class="item"
                                            :class="[ c == groupDetails.cug_flat_color ? 'active' : null]"
                                            :style="{ backgroundColor: c }" v-for="(c, i) in groupsColors"
                                            :key="'gc'+i"
                                            v-on:click="selectedColor(c)">
                                            </div>
                                        </div>
                                    </div>
                                    <div class="tab-pane fade" id="edit-pills-gradientcolor" role="tabpanel" aria-labelledby="pills-gradientcolor-tab">
                                        <label class="custom-checkbox select-random">
                                            Select Randomly
                                            <input type="checkbox" name="select_randomly" class="select_randomly" v-on:click="selectRandomGradColor" v-model="select_rand_grad_color">
                                            <span class="checkmark"></span>
                                        </label>
                                        <div class="colors colors--gradient">
                                            <div class="item"
                                            :class="[ gr.start == groupDetails.cug_gradient_start ? 'active' : null]"
                                            v-for="(gr, j) in groupsGradient" :key="'gr'+j"
                                            :style="{ backgroundImage: `linear-gradient(-90deg, ${ gr.start }, ${ gr.end })`}"
                                            v-on:click="selectedGradientColor(gr.start, gr.end)">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <SearchUser :cug_id="groupDetails.cug_id" />
                                <div class="group-users" v-show="groupUsers">
                                    <div class="item"
                                    v-for="(gu, i) in groupUsers"
                                    :key="'gu'+i"
                                    v-on:click="tempDeleteUser(gu.id)">
                                        <div class="user-img" :style="{ backgroundImage: `url('${assetUrl +'/'+ gu.clientuser.user.user_pic}')` }">
                                            <div v-if="gu.clientuser.cu_level == 'super'">
                                                <div class="user-level" style="background-color: #38ad20;"></div>
                                            </div>
                                            <div v-else-if="gu.clientuser.cu_level == 'admin'">
                                                <div class="user-level" style="background-color: #fff200;"></div>
                                            </div>
                                            <div v-else-if="gu.clientuser.cu_level == 'basic'">
                                                <div class="user-level" style="background-color: #f79118;"></div>
                                            </div>
                                        </div>
                                        <div class="user-cont">
                                            <h4 class="user-name">{{ gu.clientuser.cu_fname }} {{ gu.clientuser.cu_lname }}</h4>
                                            <p class="user-pos">{{ gu.clientuser.cu_position }}</p>
                                        </div>
                                        <div class="user-act">
                                            <span class="btn _Cust_Delete"></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" v-on:click="updateGroup">Save</button>
                        <button type="button" class="btn btn-secondary" v-on:click="hideModal">Cancel</button>
                        <button type="button" class="btn btn-delete" v-on:click="deleteGroupConfirmModal"><span class="_Inter_Delete"></span> Delete</button>
                    </div>
                </form>
            </div>
        </div>
        <DeleteGroupModal />
    </div>
</template>

<script>
import Event from '@/eventBus'
import $ from 'jquery'
import axios from 'axios'
import SearchUser from './SearchUser'
import DeleteGroupModal from './DeleteGroupModal'

import { Helpers } from '@/helpers'

export default {
    data() {
        return {
            assetUrl: Helpers.getEnv('RIIQO_APP_FILES_URL'),
            clientid: this.$store.state.rightmenu.clientid,
            clientuserurl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientuser',
            clientusergroupurl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientusergroup',
            cugmemberurl: Helpers.getEnv('CRM_BASE_URL') + '/api/cugmember',
            groupId: null,
            groupsColors: ["#00be9d","#19cf6d","#2398e1","#a462fa","#96a6a7","#00a286","#13b05d","#1c80bc","#903db0","#808d8e","#ffc54c","#f59e00","#e97f00","#e54039","#c3351f"],
            groupsGradient: [
                { start: '#00be9d', end: '#007953' },
                { start: '#19cf6d', end: '#009d4f' },
                { start: '#2398e1', end: '#004b98' },
                { start: '#a462fa', end: '#592c82' },
                { start: '#96a6a7', end: '#4e5859' },
                { start: '#00a286', end: '#006e47' },
                { start: '#13b05d', end: '#48773c' },
                { start: '#1c80bc', end: '#005587' },
                { start: '#903db0', end: '#582c5f' },
                { start: '#808d8e', end: '#54575a' },
                { start: '#ffc54c', end: '#d57f00' },
                { start: '#f59e00', end: '#b26e04' },
                { start: '#e97f00', end: '#9c6018' },
                { start: '#e54039', end: '#84332e' },
                { start: '#c3351f', end: '#a22b2a' },
            ],
            clientUserGroups: [],
            groupUsers: [],
            groupDetails: {
                cug_id: 0,
                cug_name: '',
                cug_flat_color: '',
                cug_gradient_start: '',
                cug_gradient_end: '',
                cug_is_flat: 1,
            },
            clickedUsers: [],
            isFlatColor: 1,
            select_rand_color: false,
            select_rand_grad_color: false,
            tempDeleteUserObj: [],
        }
    },
    components: {
        SearchUser,
        DeleteGroupModal
    },
    mounted () {
        let self = this;
        Event.$on('editGroup', function(id) {
            self.groupId = id;
            self.getClientGroups();
            self.showModal();
        });

        Event.$on('clickUsers', function(id) {
            self.storeTempUser(id);
        });
    },
    methods: {
        showModal () {
            $('#editGroupModal').modal('show');
            this.$emit('modalBackdrop', true);
        },
        hideModal () {
            this.tempDeleteUserObj.length = 0;
            $('#editGroupModal').modal('hide');
            this.$emit('modalBackdrop', false);
        },
        // User events
        colorTabb (tab) {
            this.isFlatColor = tab;
        },
        selectRandomColor () {
            if(!this.select_rand_color) {
                this.groupDetails.cug_flat_color = this.groupsColors[Math.floor(Math.random() * this.groupsColors.length)];
            }
        },
        selectRandomGradColor () {
            if(!this.select_rand_grad_color) {
                var rand = Math.floor(Math.random() * this.groupsGradient.length);
                this.groupDetails.cug_gradient_start = this.groupsGradient[rand].start;
                this.groupDetails.cug_gradient_end = this.groupsGradient[rand].end;
            }
        },
        selectedColor (color) {
            this.groupDetails.cug_flat_color = color;
            this.groupDetails.cug_is_flat = '1';
        },
        selectedGradientColor (startColor, endColor) {
            this.groupDetails.cug_gradient_start = startColor;
            this.groupDetails.cug_gradient_end = endColor;
            this.groupDetails.cug_is_flat = '0';
        },
        deleteGroupConfirmModal () {
            Event.$emit("deleteGroupModal", this.groupId);
        },
        storeTempUser (id) {
            if(this.clickedUsers.includes(id)) {
                for( var i = 0; i < this.clickedUsers.length; i++){
                    if ( this.clickedUsers[i] === id) {
                        this.clickedUsers.splice(i, 1);
                    }
                }
            } else {
                this.clickedUsers.push(id);
            }
        },
        tempDeleteUser (id) {
            this.groupUsers.forEach( function(v, k) {
                if(v.id == id) {
                    this.groupUsers.splice(k, 1)
                    this.tempDeleteUserObj.push(id);
                }
            }, this);
        },
        // DB actions
        getClientGroups () {
            axios
                .get(`${this.clientuserurl}/list/${this.clientid}`)
                .then( response => {
                    this.clientUserGroups = response.data.clientusergroups;
                    this.getGroupUsers();
                }).catch( (err) => {
                    console.log(err)
                })
        },
        getGroupUsers () {
            this.clientUserGroups.forEach( function(v) {
                if (v.id == this.groupId) {
                    // Set group users
                    this.groupUsers = v.cugmembers;

                    // Set group details
                    this.setGroupFormFields(v);
                }
            }, this);
        },
        setGroupFormFields (groupObj) {
            this.groupDetails.cug_id = groupObj.id;
            this.groupDetails.cug_name = groupObj.cug_name;
            if(groupObj.cug_flat_color) {
                this.groupDetails.cug_flat_color = groupObj.cug_flat_color;
            } else {
                this.groupDetails.cug_flat_color = '#00be9d';
            }

            this.groupDetails.cug_gradient_start = groupObj.cug_gradient_start;
            this.groupDetails.cug_gradient_end = groupObj.cug_gradient_end;
            if(this.groupDetails.cug_is_flat) {
                this.groupDetails.cug_is_flat = 1;
            } else {
                this.groupDetails.cug_is_flat = 0;
            }
        },
        updateGroup () {
            var frmEditCUGroup = $('#frmEditCUGroup');
            var dataString = new FormData(frmEditCUGroup[0]);
            axios({
                method: 'post',
                url: `${this.clientusergroupurl}/update/${this.groupDetails.cug_id}`,
                data: dataString,
            })
            .then( function () {
                Event.$emit("listUsers");
                // Clear values
                this.clearGroupDetails();
                // Hide the form
                this.hideModal();
            }).catch( (err) => {
                console.log(err)
            });

            // Add user to group
            if(this.clickedUsers.length) {
                this.clickedUsers.forEach( function(v) {
                    this.addUserToGroup(v, this.groupDetails.cug_id);
                }, this);
            }

            // Perform deleting of user
            if(this.tempDeleteUserObj.length) {
                this.tempDeleteUserObj.forEach( function(v) {
                    this.deleteUserFromGroup(v);
                }, this);
            }
        },
        addUserToGroup (id, groupId) {
            var dataString = new Object();
            dataString['cug_id'] = groupId;
            dataString['clientuser_id'] = id;
            axios({
                method: 'post',
                url: `${this.cugmemberurl}/store`,
                params: dataString,
            })
            .then( function () {
                Event.$emit("listUsers");
                this.updateGroupUsers(this.groupDetails.cug_id);
            }).catch( (err) => {
                console.log(err)
            });
        },
        deleteUserFromGroup (id) {
            axios({
                method: 'get',
                url: `${this.cugmemberurl}/destroy/${id}`,
            })
            .then( function () {
                Event.$emit("listUsers");
                this.updateGroupUsers(this.groupDetails.cug_id);
            }).catch( (err) => {
                console.log(err)
            });
        },
        // Clean up
        clearGroupDetails () {
            if (this.clickedUsers) {
                this.clickedUsers.length = 0;
            }
        },
        updateGroupUsers (cugId) {
            this.clientUserGroups.forEach( function(v) {
                if(v.id == cugId) {
                    this.groupUsers = v.cugmembers;
                }
            }, this);
        }
    }
}
</script>