<template>
	<div class="mod-free-chart" id="free-chart"></div>
</template>

<script>
import $ from 'jquery';
import * as d3 from "d3";

export default {
	data () {
		return {
			fdata: [{ current: 3000, total: 4000, arcValue: 45 }]
		}
	},
	mounted: function() {
		var body = $("body");
		var cont = $("#purchased-chart");
		var margin = { left: 20, right: 40, top: 1, bottom: 1 };
		var width = cont.width();
		var height = cont.height();
		var innerWidth = width - margin.left - margin.right;
		var innerHeight = height - margin.top - margin.bottom;
		//var innR = 68;
		//var outR = 72;
		//var radius = 150;
		var innR = (body.width() > 1300) ? 68 : 48;
		var outR = (body.width() > 1300) ? 72 : 52;
		var radius = (body.width() > 1300) ? 150 : 100;

		var p = Math.PI;
		var pie = d3.pie().value( function (d) {
		return d.arcValue;
		});

		var arc = d3.arc()
		.innerRadius(innR)
		.outerRadius(innR + 4)
		.startAngle(0 * (p/180))
		.endAngle( function(d) {
		return d.data.arcValue * (p/180) * p;
		});
		
		var svg = d3.select("#free-chart")
		.append("svg")
		.attr("width", width)
		.attr("height", height)
		.attr("viewBox", "0 0 "+Math.min(width,height)+" "+Math.min(width,height + margin.top + margin.bottom))
		.attr('preserveAspectRatio','xMinYMin')
		.append("g")
		.attr("transform", "translate(" + width / 2 + "," + height / 2 + ")")

		// Append Circle
		var circle = svg.append("circle")
		//.attr("r", 70)
		.attr("r", (body.width() > 1300) ? 70 : 50)
		.attr("stroke-width", 0.5)
		.attr("stroke", "#efefef")
		.attr("fill", "#14181e")

		var g = svg.selectAll("g").data(pie(this.fdata)).enter().append('g')

		// Append arc
		g.append("path")
		.attr("fill", function(d) {
		return "#0089ff";
		})
		.attr("d", arc);

		// Append the text inside the arc.
		var fo = g.append("foreignObject")
		.attr('x', -84)
		.attr('y', -30)
		.attr("width", "168")
		.attr("height", "56")

		var foText = fo.append('xhtml:div')
		.attr('class', 'chart-label')
		.attr('xmlns', 'http://www.w3.org/1999/xhtml')
		.html( function(d) {
		var str = "";
		str += '<div>'+d.data.current+'</div> <div class="total">/ '+d.data.total+'</div>';
		return str;
		});
	},
	methods: {
		drawCircleChart: function() {
		
		}
	},
	created: function (){
	  
	}
}
</script>

