<template>
    <div class="sxn-usrs sxn-client-edituser show" sxn-type="edituser" v-show="this.$store.state.rightmenu.usersTab.isEditUser">
        <form action="" method="POST" enctype="multipart/form-data" id="frmEditClientUser" v-on:submit.prevent="updateUser(clientUserObj.id)">
            <input type="hidden" name="client_id" :value="clientid">
            <div class="sca__name" v-on-clickaway="collapseFormUpload">
                <div class="col col-arrow">
                    <button type="button" class="btn btn-back" v-on:click="backToUsers"><span class="_Syst_Short_LeftArrow"></span></button>
                    <button type="button" class="btn btn-delete" v-on:click="deleteUserConfirmModal(clientUserObj.id)"><span class="_Inter_Delete"></span></button>
                </div>
                <div class="col-profile">
                    <div class="profile profile--edit"
                    v-show="!imagePreview"
                    v-on:click="showEditUpload ^= true"
                    :style="{ backgroundImage: `url('${this.assetUrl +'/'+ clientUserObj.user.user_pic}')` }">
                        <span class="_Inter_Image" v-show="!clientUserObj.user.user_pic"></span>
                        <div class="user-level"></div>
                    </div>
                    <div class="profile profile--edit"
                    v-show="imagePreview"
                    v-on:click="showEditUpload ^= true"
                    :style="{ backgroundImage: `url('${imagePreview}')` }">
                        <span class="_Inter_Image" v-show="!clientUserObj.user.user_pic"></span>
                        <div class="user-level"></div>
                    </div>
                    <div ref="editUserUpload" class="profile-upload" :class="{ open: showEditUpload }">
                        <div class="drop-file">
                            <span class="icon _Inter_Social_Storage"></span> <br/>
                            <span class="drop-file-name">DROP FILE TO UPLOAD</span>
                            <div v-for="(f, i) in files" :key="'fu'+i">
                            {{ f.name }}
                            </div>
                        </div>
                        <div class="btn btn-primary btn--uploadpic">
                            Or Browse Files
                            <input type="file" name="cupic" class="form-control-file" @change="selectedFile($event)">
                        </div>
                    </div>
                </div>
                <div class="col-name">
                    <input type="text" class="input-fname" name="cu_fname" v-model="clientUserObj.cu_fname" placeholder="User"><br/>
                    <input type="text" class="input-lname" name="cu_lname" v-model="clientUserObj.cu_lname" placeholder="Name">
                    <div class="form-group">
                        <div class="dropdown">
                            <button class="btn dropdown-toggle" type="button" id="ddEditCuLevel" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <label for="cu_level">User Type:</label> {{ clientUserObj.cu_level }}
                            </button>
                            <div class="dropdown-menu" aria-labelledby="ddEditCuLevel">
                                <a class="dropdown-item" href="#" v-on:click="setUserLevel('admin')"> <span class="legend admin"></span> Administrator</a>
                                <a class="dropdown-item" href="#" v-on:click="setUserLevel('basic')"> <span class="legend basic"></span> Basic</a>
                            </div>
                        </div>
                        <input type="text" class="input-jobtitle" name="cu_position" placeholder="Job position" v-model="clientUserObj.cu_position">
                    </div>
                </div>
                <div class="col-addgroup">
                    <div class="group-list"></div>Groups
                </div>
            </div>
            <div class="sca__content">
                <ul class="nav nav-details">
                    <li class="nav-details__item">
                        <a class="nav-link" :class="{ active: isDetails }" href="#" v-on:click="userDetailsTabbing('details')">Details</a>
                    </li>
                    <li class="nav-details__item">
                        <a class="nav-link" :class="{ active: isPermission }" href="#" v-on:click="userDetailsTabbing('perm')" v-show="clientUserObj.cu_level != 'basic'">Permissions</a>
                    </li>
                </ul>
                <div class="sca__details" v-show="userDetailsTab.isUserDetails">
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_email">Email (Username)</label>
                            <input type="email" class="input-text" name="cu_email" placeholder="name@example.com" v-model="clientUserObj.cu_email">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cupasswd">Password</label>
                            <input type="password" class="input-text" placeholder="******" v-on:click="changeUserPass">
                        </div>
                    </div>

                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_forwarding_num">Forwarding #</label>
                            <input type="text" class="input-text" name="cu_forwarding_num" value="" placeholder="(000)000-0000" v-mask="'(###)###-####'" v-model="clientUserObj.cu_forwarding_num">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_mobile_num">Cell #</label>
                            <input type="text" class="input-text phone-field" name="cu_mobile_num" placeholder="(000)000-0000" v-mask="'(###)###-####'" v-model="clientUserObj.cu_mobile_num">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_extension">Extension</label>
                            <input type="text" class="input-text" name="cu_extension" placeholder="000" v-mask="'###'" v-model="clientUserObj.cu_extension">
                        </div>
                        </div>

                        <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_street">Street Address</label>
                            <input type="text" class="input-text rm_edit_cu_address-info" id="rm_edit_cu_street_address" name="cu_street" placeholder="mailing address" v-model="clientUserObj.cu_street">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_city">City</label>
                            <input type="text" class="input-text rm_edit_cu_address-info" name="cu_city" placeholder="City" v-model="clientUserObj.cu_city">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="rm_edit_cu_administrative_area_level_1">State/Province</label>
                            <input type="text" class="input-text rm_edit_cu_address-info" name="cu_state" placeholder="State" v-model="clientUserObj.cu_state">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_postal">Zip Code</label>
                            <input type="text" class="input-text rm_edit_cu_address-info" name="cu_postal" placeholder="Zip Code" v-model="clientUserObj.cu_postal">
                        </div>
                    </div>
                </div>

                <div class="sca__perm" v-show="userDetailsTab.isUserPermission">
                    <div class="form-group">
                        <div class="switch-wrap">
                            <div class="switch" v-on:click="togglePermissions('cu_interchange_access')">
                                <input type="checkbox" class="switch__checkbox" :checked="selectedPerm.includes('cu_interchange_access')">
                                <div class="slider"></div>
                            </div>
                            Allowed to access interchange
                        </div>
                        <div class="switch-wrap">
                            <div class="switch" v-on:click="togglePermissions('cu_can_edit_interchange')">
                                <input type="checkbox" class="switch__checkbox" :checked="selectedPerm.includes('cu_can_edit_interchange')">
                                <div class="slider"></div>
                            </div>
                            Allowed to make changes in interchange
                        </div>
                        <div class="switch-wrap">
                            <div class="switch" v-on:click="togglePermissions('cu_assigned_to_all')">
                                <input type="checkbox" class="switch__checkbox" :checked="selectedPerm.includes('cu_assigned_to_all')">
                                <div class="slider"></div>
                            </div>
                            Assign to all customers and affiliates
                        </div>
                        <div class="switch-wrap">
                            <div class="switch" v-on:click="togglePermissions('cu_view_storage')">
                                <input type="checkbox" class="switch__checkbox" :checked="selectedPerm.includes('cu_view_storage')">
                                <div class="slider"></div>
                            </div>
                            Allowed to view storage module
                        </div>
                        <div class="switch-wrap">
                            <div class="switch" v-on:click="togglePermissions('cu_view_analytics')">
                                <input type="checkbox" class="switch__checkbox" :checked="selectedPerm.includes('cu_view_analytics')">
                                <div class="slider"></div>
                            </div>
                            Allowed to view analytics
                        </div>
                    </div>
                </div>
                <div class="form-group">
                    <button class="btn btn-primary btn--updateUser">Save</button>
                    <button class="btn btn--cancelAddUser" @click.prevent="backToUsers">Cancel</button>
                </div>
            </div>
            <div class="sca__stats"></div>
        </form>

        <!-- Error adding of users modals -->
        <div class="modal modal-notif modal-notif--warn" tabindex="-1" role="dialog" id="editUsersErrModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="text-center">
                            <span class="modal-icon _Inter_Warning_Outlined"></span>
                            <h3>Error</h3>
                            <p>{{ errMsg }}</p>
                            <ul>
                                <li v-for="(e, i) in errs" :key="'err'+i"><span class="x-icon _Inter_Close_Outlined"></span> {{ e }}</li>
                            </ul>
                        </div>
                        <div class="btn-action">
                            <button type="button" class="btn" data-dismiss="modal">Ok</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import { directive as onClickaway } from 'vue-clickaway';
import { VueMaskDirective } from 'v-mask'
import Event from '@/eventBus'
import axios from 'axios'

import { Helpers } from '@/helpers'

export default {
    props: {
        usersObj: Array,
    },
    data() {
        return {
            clientid: this.$store.state.rightmenu.clientid,
            assetUrl: Helpers.getEnv('RIIQO_APP_FILES_URL'),
            clientUserUrl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientuser',
            usersTabState: {
                isUserList: true,
                isAddUser: false,
                isEditUser: false,
                isGroupList: false,
            },
            showEditUpload: false,
            file: '',
            files: [],
            imagePreview: '',
            clientUserObj: {
                id: null,
                cu_fname: '',
                cu_lname: '',
                cu_level: '',
                cu_position: '',
                cu_email: '',
                cu_forwarding_num: '',
                cu_mobile_num: '',
                cu_extension: '',
                cu_street: '',
                cu_city: '',
                cu_state: '',
                cu_postal: '',
                cu_interchange_access: 0,
                cu_can_edit_interchange: 0,
                cu_assigned_to_all: 0,
                cu_view_storage: 0,
                cu_view_analytics: 0,
                cupic: '',
                user: { }
            },
            selectedPerm: [],
            isDetails: true,
            isPermission: false,
            userDetailsTab: {
                isUserDetails: true,
                isUserPermission: false,
            },
            // Notifications properties
            errMsg: '',
            errs: []
        }
    },
    directives: {
        onClickaway: onClickaway,
        mask: VueMaskDirective,
    },
    mounted() {
        let self = this;

        // Set the clientUserObj
        Event.$on("setUserFields", function(id) {
            // console.log(id);
            self.extractUserDetails(id)
        });
    },
    methods: {
        extractUserDetails (id) {
            this.usersObj.forEach( function(v) {
                if(v.id == id) {
                    this.clientUserObj['id'] = v.id;
                    this.clientUserObj['cu_fname'] = v.cu_fname;
                    this.clientUserObj['cu_lname'] = v.cu_lname;
                    this.clientUserObj['cu_level'] = v.cu_level;
                    this.clientUserObj['cu_position'] = v.cu_position;
                    this.clientUserObj['cu_email'] = v.cu_email;
                    this.clientUserObj['cu_forwarding_num'] = v.cu_forwarding_num;
                    this.clientUserObj['cu_mobile_num'] = v.cu_mobile_num;
                    this.clientUserObj['cu_extension'] = v.cu_extension;
                    this.clientUserObj['cu_street'] = v.cu_street;
                    this.clientUserObj['cu_city'] = v.cu_city;
                    this.clientUserObj['cu_state'] = v.cu_state;
                    this.clientUserObj['cu_postal'] = v.cu_postal;
                    this.clientUserObj['cu_interchange_access'] = this.filterUserPerm(v.cu_interchange_access);
                    this.clientUserObj['cu_can_edit_interchange'] = this.filterUserPerm(v.cu_can_edit_interchange);
                    this.clientUserObj['cu_assigned_to_all'] = this.filterUserPerm(v.cu_assigned_to_all);
                    this.clientUserObj['cu_view_storage'] = this.filterUserPerm(v.cu_view_storage);
                    this.clientUserObj['cu_view_analytics'] = this.filterUserPerm(v.cu_view_analytics);
                    this.clientUserObj['user'] = v.user;
                }
            }, this);
        },
        updateUser (cuid) {
            var dataString = new FormData();

            // Append fields from v-model
            Object.keys(this.clientUserObj).forEach( function(k) {
                dataString.append(k, this.clientUserObj[k]);
            }, this);

            // Delete user key

            // Append token and client id
            dataString.append('client_id', this.clientid);

            // Check drag n drop image upload
            if(this.files.length) {
                dataString.append('cupic', this.files[0]);
            }
            if(this.file) {
                dataString.append('cupic', this.file);
            }

            axios({
                method: 'post',
                url: this.clientUserUrl +'/update/'+cuid,
                data: dataString,
            })
            .then( function () {
                // Refresh user list
                Event.$emit("listUsers");

                // Reset form andn values
                this.clearClientUserObj();

                // Return to user list
                this.setUsersTab('isUserList', true);
            }).catch( (err) => {
                console.log(err);
                if(err != null) {
                    this.editUsersErrModal(err.response.data.message);
                    this.errs = err.response.data.errors;
                }
            });

            this.showUploadForm = false;
        },
        collapseFormUpload () {
            this.showEditUpload = false;
        },
        // State
        backToUsers: function() {
            this.$store.commit('toggleUsersTab', 'isUserList');
            this.imagePreview = null;
        },
        // Modals
        changeUserPass: function() {
            Event.$emit("changeUserPassModal", this.clientUserObj.id);
        },
        deleteUserConfirmModal () {
            Event.$emit("deleteUserModal", this.clientUserObj.id);
        },
        selectedFile: function(file) {
            this.files = file.target.files;
            this.getImagePreviews();
            this.showEditUpload = false;
        },
        userDetailsTabbing: function(tab) {
            if(tab == 'details') {
                this.isDetails = true;
                this.isPermission = false;
                this.userDetailsTab.isUserDetails = true;
                this.userDetailsTab.isUserPermission = false;
            } else if(tab == 'perm') {
                this.isDetails = false;
                this.isPermission = true;
                this.userDetailsTab.isUserDetails = false;
                this.userDetailsTab.isUserPermission = true;
            }
        },
        filterUserPerm: function(val) {
            return (val == true) ? 1 : 0;
        },
        togglePermissions: function(perm) {
            if(this.selectedPerm.includes(perm)) {
                this.selectedPerm.splice(this.selectedPerm.findIndex(v => v === perm), 1);
                this.togglePermVal(perm, 0);
            } else {
                this.selectedPerm.push(perm);
                this.togglePermVal(perm, 1);
            }
        },
        togglePermVal: function(perm, val) {
            return this.clientUserObj[perm] = val;
        },
    },
}
</script>