<template>
    <div class="modal modal-notif modal-notif--warn" tabindex="-1" role="dialog" id="deleteGroupModal">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="text-center">
                        <span class="modal-icon _Inter_Warning_Outlined"></span>
                        <h3>Delete Group?</h3>
                        <p>Are you sure you want to delete this group?</p>
                    </div>
                    <div class="btn-action">
                        <button type="button" class="btn btn-primary" v-on:click="deleteGroup">Delete</button>
                        <button type="button" class="btn" data-dismiss="modal">Cancel</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
import axios from 'axios'
import Event from '@/eventBus'
import { Helpers } from '@/helpers'

export default {
    data() {
        return {
            groupId: null,
            clientusergroupurl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientusergroup',
        }
    },
    mounted: function() {
        let self = this;

        // Set the clientUserObj
        Event.$on("deleteGroupModal", function(id) {
            // console.log(id);
            self.groupId = id;
            self.showModal();
        });
    },
    methods: {
        showModal: function() {
            $('#deleteGroupModal').modal('show');
        },
        hideModal: function() {
            $('#deleteGroupModal').modal('hide');
        },
        hideEditModal: function() {
            $('#editGroupModal').modal('hide');
        },
        // DB actions
        deleteGroup: function() {
            axios({
                method: 'get',
                url: `${this.clientusergroupurl}/destroy/${this.groupId}`,
            })
            .then( function () {
                // Refresh users list
                Event.$emit("listUsers");

                // Hide modals
                this.hideModal();
                this.hideEditModal();

            }).catch( (err) => {
                console.log(err)
            });
        },

    },
}
</script>