<template>
<div>
    <div id="rightmenu" class="rightmenu"
        v-bind:class="[
            // Default showing of menu
            { show: showMenu },
            //{ showPanels: isRightSubmenu },
            activeSubMenu
        ]"
        v-on-clickaway="hideAllRightMenus"
        >
        <RightMenuSubs
            v-bind:show-menu="showMenu"
            :clientid='activeuser.user.activeuser.client_id'
            :activeuser=activeuser
            :isRightMenu='isRightMenu'
            :activeSubMenu='activeSubMenu'
            v-on:setActiveRightMenu='setActiveSubMenu'
            v-on:modalBackdrop="showModalBackdrop"/>
        <RightMenuTabs
            v-bind:show-menu="showMenu"
            :username='activeuser.user.name'
            :isRightMenu='isRightMenu'
            :activeSubMenu='activeSubMenu'
            v-on:setActiveRightMenu='setActiveSubMenu'
            />
        <div class="modal-backdrop"
            :class="{ show: isModalBackdrop }"></div>
    </div>
</div>
</template>

<script>
import Vue from 'vue'
import RightMenuTabs from './RightMenuTabs'
import RightMenuSubs from './RightMenuSubs'
import { directive as onClickaway } from 'vue-clickaway';

export default Vue.extend({
    data() {
        return {
            isRightMenu: false,
            activeSubMenu: null,
            isModalBackdrop: false
        }
    },
    methods: {
        setActiveSubMenu (mysubmenu) {
            if(this.activeSubMenu == mysubmenu) {
                this.activeSubMenu = '';
            } else {
                this.activeSubMenu = mysubmenu;
            }
            console.log(mysubmenu);
        },
        hideAllRightMenus: function() {
            this.activeSubMenu = '';
        },
        // Modal backdrop
        showModalBackdrop: function(bool) {
            this.isModalBackdrop = bool;
        }
    },
    components: {
        RightMenuTabs,
        RightMenuSubs
    },
    directives: {
        onClickaway: onClickaway
    },
    props: {
        activeuser: {
            type: Object
        },
        showMenu: {
            type: Boolean
        }
    },
    created() {

    },
});
</script>

<style lang="scss" scoped>

</style>
