<template>
    <!-- Add Group -->
    <div class="modal" tabindex="-1" role="dialog" id="addGroupModal">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <form action="" method="post" enctype="multipart/form-data" id="frmAddCUGroup">
                    <div class="modal-header">
                        <h5 class="modal-title">Create New User Group</h5>
                        <button type="button" class="close" aria-label="Close" v-on:click="hideModal"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <input type="hidden" name="client_id" :value="clientid">
                        <input type="hidden" name="cug_flat_color" :value="flatColor">
                        <input type="hidden" name="cug_gradient_start" :value="gradientStartColor">
                        <input type="hidden" name="cug_gradient_end" :value="gradientEndColor">
                        <input type="hidden" name="cug_is_flat" :value="isFlatColor">
                        <div class="row">
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="cug_name">NAME GROUP</label>
                                    <input type="text" class="input-text" name="cug_name" aria-describedby="emailHelp" placeholder="Client group name">
                                </div>
                                <label class="lbl-group-color">GROUP COLOR</label>
                                <div v-if="isFlatColor == '1'">
                                    <div class="selected-color" :style="{ backgroundColor: flatColor }"></div>
                                </div>
                                <div v-if="isFlatColor == '0'">
                                    <div class="selected-color" :style="{ backgroundImage: `linear-gradient(-90deg, ${ gradientStartColor }, ${ gradientEndColor })` }"></div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-8">
                                        <ul class="nav nav-pills nav-pills--colors" id="pills-tab-color" role="tablist">
                                            <li class="nav-item">
                                                <a class="nav-link active" id="pills-flatcolor-tab" data-toggle="pill" href="#pills-flatcolor" role="tab" aria-controls="pills-flatcolor" aria-selected="true" v-on:click="colorTabb('1')">Flat</a>
                                            </li>
                                            <li class="nav-item">
                                                <a class="nav-link" id="pills-gradientcolor-tab" data-toggle="pill" href="#pills-gradientcolor" role="tab" aria-controls="pills-gradientcolor" aria-selected="false" v-on:click="colorTabb('0')">Gradient</a>
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-sm-4 col-hex">
                                        <div class="hex">{{ flatColor }}</div>
                                    </div>
                                </div>
                                <div class="tab-content tab-content--colors" id="pills-color-tabContent">
                                    <div class="tab-pane fade show active" id="pills-flatcolor" role="tabpanel" aria-labelledby="pills-flatcolor-tab">
                                    <label class="custom-checkbox select-random">
                                        <input type="checkbox" name="select_rand_color" class="select_randomly" v-on:click="selectRandomColor" v-model="select_rand_color">
                                        <span class="checkmark"></span>
                                        Select Randomly
                                    </label>
                                    <div class="colors">
                                        <div class="item"
                                        :style="{ backgroundColor: c }" v-for="(c, i) in groupsColors"
                                        :key="'gc'+i"
                                        v-on:click="selectedColor(c)">
                                        </div>
                                    </div>
                                    </div>
                                    <div class="tab-pane fade" id="pills-gradientcolor" role="tabpanel" aria-labelledby="pills-gradientcolor-tab">
                                        <label class="custom-checkbox select-random">
                                            Select Randomly
                                            <input type="checkbox" name="select_rand_grad_color" class="select_randomly" v-on:click="selectRandomGradColor" v-model="select_rand_grad_color">
                                            <span class="checkmark"></span>
                                        </label>
                                        <div class="colors colors--gradient">
                                            <div class="item"
                                            v-for="(gr, j) in groupsGradient" :key="'gr'+j"
                                            :style="{ backgroundImage: `linear-gradient(-90deg, ${ gr.start }, ${ gr.end })`}"
                                            v-on:click="selectedGradientColor(gr.start, gr.end)">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <SearchAvailableUsers />
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" v-on:click="addNewGroup">Create</button>
                        <button type="button" class="btn btn-secondary" v-on:click="hideModal">Cancel</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import Event from '@/eventBus'
import $ from 'jquery'
import axios from 'axios'
import SearchAvailableUsers from './SearchAvailableUsers'
import { Helpers } from '@/helpers'

export default {
    data() {
        return {
            clientid: this.$store.state.rightmenu.clientid,
            clientusergroupurl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientusergroup',
            cugmemberurl: Helpers.getEnv('CRM_BASE_URL') + '/api/cugmember',
            groupsColors: ["#00be9d","#19cf6d","#2398e1","#a462fa","#96a6a7","#00a286","#13b05d","#1c80bc","#903db0","#808d8e","#ffc54c","#f59e00","#e97f00","#e54039","#c3351f"],
            groupsGradient: [
                { start: '#00be9d', end: '#007953' },
                { start: '#19cf6d', end: '#009d4f' },
                { start: '#2398e1', end: '#004b98' },
                { start: '#a462fa', end: '#592c82' },
                { start: '#96a6a7', end: '#4e5859' },
                { start: '#00a286', end: '#006e47' },
                { start: '#13b05d', end: '#48773c' },
                { start: '#1c80bc', end: '#005587' },
                { start: '#903db0', end: '#582c5f' },
                { start: '#808d8e', end: '#54575a' },
                { start: '#ffc54c', end: '#d57f00' },
                { start: '#f59e00', end: '#b26e04' },
                { start: '#e97f00', end: '#9c6018' },
                { start: '#e54039', end: '#84332e' },
                { start: '#c3351f', end: '#a22b2a' },
            ],
            flatColor: '#00be9d',
            isFlatColor: 1,
            gradientStartColor: '',
            gradientEndColor: '',
            clickedUsers: [],
            select_rand_color: false,
            select_rand_grad_color: false
        }
    },
    methods: {
        showModal: function() {
            $('#addGroupModal').modal('show');
            this.$emit('modalBackdrop', true);
        },
        hideModal: function() {
            $('#addGroupModal').modal('hide');
            this.$emit('modalBackdrop', false);
        },
        colorTabb: function(tab) {
            this.isFlatColor = tab;
        },
        selectRandomColor: function() {
            if(!this.select_rand_color) {
                this.flatColor = this.groupsColors[Math.floor(Math.random() * this.groupsColors.length)];
            }
        },
        selectRandomGradColor: function() {
            if(!this.select_rand_grad_color) {
                var rand = Math.floor(Math.random() * this.groupsGradient.length);
                this.gradientStartColor = this.groupsGradient[rand].start;
                this.gradientEndColor = this.groupsGradient[rand].end;
            }
        },
        selectedColor: function(color) {
            this.flatColor = color;
            this.isFlatColor = '1';
        },
        selectedGradientColor: function(startColor, endColor) {
            this.gradientStartColor = startColor;
            this.gradientEndColor = endColor;
            this.isFlatColor = '0';
        },
        storeTempUser: function(id) {
            if(this.clickedUsers.includes(id)) {
                for( var i = 0; i < this.clickedUsers.length; i++){
                    if ( this.clickedUsers[i] === id) {
                        this.clickedUsers.splice(i, 1);
                    }
                }
            } else {
                this.clickedUsers.push(id);
            }
        },
        // Database actions
        addNewGroup: function() {
            var frmAddCUGroup = $('#frmAddCUGroup');
            var dataString = new FormData(frmAddCUGroup[0]);
            axios({
                method: 'post',
                url: `${this.clientusergroupurl}/store/${this.clientid}`,
                data: dataString,
            })
            .then( response => {
                Event.$emit("listUsers");

                //Add user to group
                if(this.clickedUsers.length) {
                    this.clickedUsers.forEach( function(v) {
                        this.addUserToGroup(v, response.data.id);
                    }, this);
                }

                // Clear values
                this.clearGroupDetails();

                // Reset form
                frmAddCUGroup[0].reset();

                // Hide the form
                this.hideModal();

            }).catch( (err) => {
                console.log(err)
            });
        },
        addUserToGroup: function(id, groupId) {
            //var dataString = new FormData();
            var dataString = new Object();
            dataString['cug_id'] = groupId;
            dataString['clientuser_id'] = id;
            axios({
                method: 'post',
                url: `${this.cugmemberurl}/store`,
                params: dataString,
            })
            .then( function () {
                Event.$emit("listUsers");
                // this.updateGroupUsers(this.groupDetails.cug_id);
            }).catch( (err) => {
                console.log(err)
            });
        },
        // Clear values
        clearGroupDetails: function() {
            this.flatColor = '';
            this.isFlatColor = 0;
            this.gradientStartColor = '';
            this.gradientEndColor = '';
            this.clickedUsers.length = 0;
        }
    },
    mounted: function() {
        let self = this;

        Event.$on("addGroup", function() {
            self.showModal();
        });

        Event.$on('clickusers', function(id) {
            self.storeTempUser(id);
        });
    },
    components: {
        SearchAvailableUsers
    }
}
</script>