export default {
    state: {
        closeallrightmenu: false,
        displayrightmenu: false,
        rightSubmenuStatusFlag: false,
        clientid: null,
        usersTab: {
            isUserList: true,
            isAddUser: false,
            isEditUser: false,
            isGroupList: false,
        },
        curEditedUser: 0
    },
    mutations: {
        setCloseRightMenuFlag (state, mybool = true) {
            state.closeallrightmenu = mybool
        },
        setRightMenuDisplay (state) {
            state.displayrightmenu = !state.displayrightmenu
        },
        setRightMenuClientId (state, myId) {
            state.clientid = myId
        },
        toggleUsersTab (state, myusertab) {
            Object.keys(state.usersTab).forEach(function (k) {
                if (k == myusertab) {
                    state.usersTab[k] = true
                } else {
                    state.usersTab[k] = false
                }
            })
        },
        editedUser (state, id) {
            state.curEditedUser = id;
        }
    }
}