<template>
    <div class="sxn-usrs sxn-client-adduser show" sxn-type="adduser" v-show="$store.state.rightmenu.usersTab.isAddUser">
        <form enctype="multipart/form-data" id="frmAddClientUser" v-on:submit.prevent="storeNewUser">
            <input type="hidden" name="client_id" :value="clientid">
            <div class="sca__name" v-on-clickaway="collapseFormUpload">
                <!-- Image upload section -->
                <div class="col-profile">
                    <div class="profile"
                        v-on:click="showUpload = true"
                        v-show="!imagePreview"><span class="_Inter_Image"></span>
                    </div>
                    <div class="profile"
                        v-on:click="showUpload = true"
                        v-show="imagePreview"
                        :style="{ backgroundImage: `url('${imagePreview}')` }">
                    </div>
                    <div ref="addUserUpload" class="profile-upload" :class="{ open: showUpload }">
                        <div class="drop-file">
                            <span class="icon _Inter_Social_Storage"></span> <br/>
                            <span class="drop-file-name">DROP FILE TO UPLOAD</span>
                            <div v-for="(uf, i) in files" :key="'uf'+i">
                            {{ uf.name }}
                            </div>
                        </div>
                        <div class="btn btn-primary btn--uploadpic">
                            Or Browse Files <input type="file" name="cupic" class="form-control-file" @change="selectedFile($event)">
                        </div>
                    </div>
                </div>
                <!-- Name section -->
                <div class="col-name">
                    <input type="text" class="input-fname" name="cu_fname" placeholder="User" v-model="clientUserObj.cu_fname"><br/>
                    <input type="text" class="input-lname" name="cu_lname" placeholder="Name" v-model="clientUserObj.cu_lname">
                    <div class="form-group">
                        <div class="dropdown">
                            <button class="btn dropdown-toggle" type="button" id="ddAddCuLevel" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <label for="cu_level">User Type:</label> {{ clientUserObj.cu_level }}
                            </button>
                            <div class="dropdown-menu" aria-labelledby="ddAddCuLevel">
                                <a class="dropdown-item" href="#" v-on:click="setUserLevel('admin')"> <span class="legend admin"></span> Administrator</a>
                                <a class="dropdown-item" href="#" v-on:click="setUserLevel('basic')"> <span class="legend basic"></span> Basic</a>
                            </div>
                        </div>
                        <input type="text" class="input-jobtitle" name="cu_position" placeholder="Job position" v-model="clientUserObj.cu_position">
                    </div>
                </div>
                <!-- Group section -->
                <div class="col-addgroup">
                    <div class="dropdown dropdown--addgroup">
                        <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownAddGroupBtn" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span class="_Syst_AddCircle"></span>
                        </button>
                        <div class="dropdown-menu" aria-labelledby="dropdownAddGroupBtn">
                            <div class="search-group-wrap">
                                <div class="search-field">
                                    <label for="search_group">Search Group</label>
                                    <input type="text" class="input-text input-text--search" name="search_group" aria-describedby="" placeholder="Search Group">
                                    <hr />
                                </div>
                                <ul class="search-res"></ul>
                            </div>
                        </div>
                        <input type="hidden" value="" name="cug_id">
                    </div>
                    Groups
                </div>
            </div>
            <div class="sca__content">
                <!-- Details section -->
                <div class="sca__details">
                    <h2 class="title">Details</h2>
                    <!-- User section -->
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_email">Email (Username)</label>
                            <input type="email" class="input-text" name="cu_email" placeholder="name@example.com" id="input-text-aue" v-model="clientUserObj.cu_email">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cupasswd">Password <span class="_Inter_Hide"></span></label>
                            <input type="password" class="input-text" name="cupassword" autocomplete="false" v-model="clientUserObj.cupassword">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cupasswdrpt">Repeat password <span class="_Inter_Hide"></span></label>
                            <input type="password" class="input-text" name="cupasswordrpt" autocomplete="false" v-model="clientUserObj.cupasswordrpt">
                        </div>
                    </div>
                    <!-- Phone section -->
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_forwarding_num">Forwarding #</label>
                            <input type="text" class="input-text" name="cu_forwarding_num" value="" placeholder="(000)000-0000" v-mask="'(###)###-####'" v-model="clientUserObj.cu_forwarding_num">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_mobile_num">Cell #</label>
                            <input type="text" class="input-text phone-field" name="cu_mobile_num" placeholder="(000)000-0000" v-mask="'(###)###-####'" v-model="clientUserObj.cu_mobile_num">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_extension">Extension</label>
                            <input type="text" class="input-text" name="cu_extension" placeholder="000" v-mask="'###'" v-model="clientUserObj.cu_extension">
                        </div>
                    </div>
                    <!-- Address section -->
                    <div class="row">
                        <div class="form-group col-sm-3">
                            <label for="cu_street">Street Address</label>
                            <input type="text" class="input-text rm_add_cu_address-info" id="rm_add_cu_street_address" name="cu_street" placeholder="mailing address" v-model="clientUserObj.cu_street">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_city">City</label>
                            <input type="text" class="input-text rm_add_cu_address-info" id="rm_add_cu_locality" name="cu_city" placeholder="city" v-model="clientUserObj.cu_city">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="rm_add_cu_administrative_area_level_1">State/Province</label>
                            <input type="text" class="input-text rm_add_cu_address" id="rm_add_cu_administrative_area_level_1" name="cu_state" value="" placeholder="State/Province" v-model="clientUserObj.cu_state">
                        </div>
                        <div class="form-group col-sm-3">
                            <label for="cu_postal">Zip Code</label>
                            <input type="text" class="input-text rm_add_cu_address-info" id="rm_add_cu_postal_code" name="cu_postal" placeholder="Zip Code" v-model="clientUserObj.cu_postal">
                        </div>
                    </div>
                </div>
                <!-- Permissions section -->
                <div class="sca__perm">
                    <h2 class="title">Permissions</h2>
                    <div class="form-group row">
                        <div class="col">
                            <div class="switch-wrap">
                                <div class="switch" v-on:click="togglePermissions('cu_interchange_access')">
                                    <input type="checkbox" class="switch__checkbox" name="cu_interchange_access" value="0" :checked="selectedPerm.includes('cu_interchange_access')">
                                    <div class="slider"></div>
                                </div>
                                Allowed to access interchange
                            </div>
                            <div class="switch-wrap">
                                <div class="switch" v-on:click="togglePermissions('cu_can_edit_interchange')">
                                    <input type="checkbox" class="switch__checkbox" name="cu_can_edit_interchange" value="0" :checked="selectedPerm.includes('cu_can_edit_interchange')">
                                    <div class="slider"></div>
                                </div>
                                Allowed to make changes in interchange
                            </div>
                            <div class="switch-wrap">
                                <div class="switch" v-on:click="togglePermissions('cu_assigned_to_all')">
                                    <input type="checkbox" class="switch__checkbox" name="cu_assigned_to_all" value="0" :checked="selectedPerm.includes('cu_assigned_to_all')">
                                    <div class="slider"></div>
                                </div>
                                Assign to all customers and affiliates
                            </div>
                        </div>
                        <div class="col">
                            <div class="switch-wrap">
                                <div class="switch" v-on:click="togglePermissions('cu_view_storage')">
                                    <input type="checkbox" class="switch__checkbox" name="cu_view_storage" value="0" :checked="selectedPerm.includes('cu_view_storage')">
                                    <div class="slider"></div>
                                </div>
                                Allowed to view storage module
                            </div>
                            <div class="switch-wrap">
                                <div class="switch" v-on:click="togglePermissions('cu_view_analytics')">
                                    <input type="checkbox" class="switch__checkbox" name="cu_view_analytics" value="0" :checked="selectedPerm.includes('cu_view_analytics')">
                                    <div class="slider"></div>
                                </div>
                                Allowed to view analytics
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Submit section -->
                <div class="form-group">
                    <button class="btn btn-primary">Create New User</button>
                    <button class="btn btn--cancelAddUser" @click.prevent="backToUsers">Cancel</button>
                </div>
            </div>
        </form>

        <div class="modal modal-notif modal-notif--warn" tabindex="-1" role="dialog" id="addUsersErrModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="text-center">
                            <span class="modal-icon _Inter_Warning_Outlined"></span>
                            <h3>Error</h3>
                            <p>{{ errMsg }}</p>
                            <ul>
                                <li v-for="(e, i) in errs" :key="'err'+i"><span class="x-icon _Inter_Close_Outlined"></span> {{ e }}</li>
                            </ul>
                        </div>
                        <div class="btn-action">
                            <button type="button" class="btn" data-dismiss="modal">Ok</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { directive as onClickaway } from 'vue-clickaway';
import { VueMaskDirective } from 'v-mask'
import axios from 'axios'
import $ from 'jquery'
import { Helpers } from '@/helpers'

export default {
    directives: {
        onClickaway: onClickaway,
        mask: VueMaskDirective,
    },
    data() {
        return {
            clientid: this.$store.state.rightmenu.clientid,
            showUpload: false,
            files: [],
            imagePreview: '',
            clientUserObj: {
                cu_fname: '',
                cu_lname: '',
                cu_level: 'basic',
                cu_position: '',
                cu_email: '',
                cupassword: '',
                cupasswordrpt: '',
                cu_forwarding_num: '',
                cu_mobile_num: '',
                cu_extension: '',
                cu_street: '',
                cu_city: '',
                cu_state: '',
                cu_postal: '',
                cu_interchange_access: 0,
                cu_can_edit_interchange: 0,
                cu_assigned_to_all: 0,
                cu_view_storage: 0,
                cu_view_analytics: 0,
                cupic: '',
                //_token: null,
                //client_id: null
            },
            selectedPerm: [],
            // Notifications properties
            errMsg: '',
            errs: [],
            clientUserUrl: Helpers.getEnv('CRM_BASE_URL') + '/api/clientuser',
        }
    },
    methods: {
        storeNewUser: function() {
            var dataString = new FormData();

            // Append fields from v-model
            Object.keys(this.clientUserObj).forEach( function(k) {
                dataString.append(k, this.clientUserObj[k]);
            }, this);

            // Append token and client id
            dataString.append('client_id', this.clientid);

            // Check drag n drop image upload
            if(this.files.length) {
                dataString.append('cupic', this.files[0]);
            }
            if(this.file) {
                dataString.append('cupic', this.file);
            }

            axios({
                method: 'post',
                url: this.clientUserUrl+'/store',
                data: dataString
            })
            .then( function () {
                // Refresh user list
                Event.$emit("listUsers");

                // Reset form andn values
                this.file = null;
                this.files = [];
                this.clearClientUserObj();
                // Return to user list
                this.$store.commit('toggleUsersTab', 'isUserList');
            }).catch( (err) => {
                console.log(err);
                if(err != null) {
                    this.addUsersErrModal(err.response.data.message);
                    this.errs = err.response.data.errors;
                }
            });

            this.showUploadForm = false;
        },
        collapseFormUpload: function() {
            this.showUpload = false;
        },
        setUserLevel: function(level) {
            this.clientUserObj.cu_level = level;
        },
        togglePermissions: function(perm) {
            if(this.selectedPerm.includes(perm)) {
                this.selectedPerm.splice(this.selectedPerm.findIndex(v => v === perm), 1);
                this.togglePermVal(perm, 0);
            } else {
                this.selectedPerm.push(perm);
                this.togglePermVal(perm, 1);
            }
        },
        togglePermVal: function(perm, val) {
            return this.clientUserObj[perm] = val;
        },
        clearClientUserObj: function() {
            Object.keys(this.clientUserObj).forEach( function(k) {
                this.clientUserObj[k] = '';
            }, this);
        },
        // Notifications
        addUsersErrModal: function(msg) {
            this.errMsg = msg;
            $('#addUsersErrModal').modal('show');
        },
        backToUsers: function() {
            this.$store.commit('toggleUsersTab', 'isUserList');
            this.imagePreview = null;
        },
    }
}
</script>