<template>
    <div class="sxn-usrs sxn-client-groups show" sxn-type="groups" v-show="$store.state.rightmenu.usersTab.isGroupList">
        <div class="btn-group btn-group--users" role="group" aria-label="Basic example">
            <button type="button" class="btn tab-nav-btn" tab-type="users" v-on:click="setUsersTab('isUserList')">Users</button>
            <button type="button" class="btn tab-nav-btn active" tab-type="groups" v-on:click="setUsersTab('isGroupList')">Groups</button>
            <button type="button" class="btn btn--addgroups" v-on:click="addGroupModal"><span class="_Syst_AddCircle"></span></button>
        </div>
        <ul class="mod-usr-lgnd">
            <li class="mod-usr-lgnd__item"><span class="sa"></span> Super Admin</li>
            <li class="mod-usr-lgnd__item"><span class="ad"></span> Admin</li>
            <li class="mod-usr-lgnd__item"><span class="ba"></span> Basic</li>
        </ul>
        <div ref="rcarousel" class="rcarousel" id="rcarousel-groups"
            v-if="groupsObj">
            <div class="rcarousel-content">
                <div ref="cgroupsgrid" class="cgroups-grid" id="cgroups-grid">
                    <div class="cgroups-grid__item"
                    v-for="(group, i) in groupsObj"
                    :key="'grp'+i">
                        <div class="group-name" :data-groupid="group.id" v-on:click="editGroupModal(group.id)">
                            <div class="top-bar" :style="{ backgroundColor: group.cug_flat_color }" v-show="group.cug_flat_color"></div>
                            <div class="top-bar" :style="{ backgroundImage: `linear-gradient(-90deg, '${ group.cug_gradient_start }', '${ group.cug_gradient_end }')` }" v-show="!group.cug_flat_color"></div>
                            <h4 class="group-name__name">{{ group.cug_name }}</h4>
                            <span class="_Syst_AddCircle btn-edit-group" :style="{ color: group.cug_flat_color }"></span>
                        </div>
                        <div class="group-user"
                            v-for="(gm, j) in group.cugmembers" :key="'gm'+j">
                            <div class="user-img" :style="{ backgroundImage: `url('${assetUrl +'/'+ gm.clientuser.user.user_pic}')` }">
                                <span class="_Cust_Customers" v-show="!gm.clientuser.user.user_pic"></span>
                                <div class="user-level" :style="{ backgroundColor: gm.cug_flat_color }"></div>
                            </div>
                            <div class="user-cont">
                                <h4 class="user-name">{{ gm.clientuser.cu_fname }} {{ gm.clientuser.cu_lname }}</h4>
                                <span class="user-pos">{{ gm.clientuser.cu_position }} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="rcarousel-arrow">
                <span class="_Syst_RightArrow" v-on:click="nextSlide" v-show="isGroupNext"></span>
            </div>
            <div class="rcarousel-arrow-prev">
                <span class="_Syst_LeftArrow" v-on:click="prevSlide" v-show="isGroupPrev"></span>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import Event from '@/eventBus'
import $ from 'jquery'

import { Helpers } from '@/helpers'

export default {
    props: {
        groupsObj: Array,
    },
    data() {
        return {
            assetUrl: Helpers.getEnv('RIIQO_APP_FILES_URL'),
            isGroupNext: true,
            isGroupPrev: false,
            usersTabState: {
                isUserList: true,
                isAddUser: false,
                isEditUser: false,
                isGroupList: false,
            },
            slide: {
                currentPos: 0,
                slideInterval: 0
            },
        }
    },
    methods: {
        setUsersTab: function(view) {
            this.$store.commit('toggleUsersTab', view);
        },
        addGroupModal: function() {
            // caught by AddGroup component
            Event.$emit("addGroup");
        },
        editGroupModal: function(id) {

            Event.$emit("editGroup", id);
        },
        // Carousel actions
        prevSlide: function() {
            var groupsLen = this.groupsObj.length;
            var groupItem = $('.cgroups-grid__item').width();
            var groupContentTotal = groupsLen * groupItem;
            this.slide.slideInterval = this.$refs.rcarousel.clientWidth;
            if(this.slide.currentPos > 0) {
                this.slide.currentPos -= this.slide.slideInterval;
                $('#cgroups-grid').css({ transform: 'translateX(-'+ this.slide.currentPos +'px)' });
            } else {
                this.isGroupPrev = false;
            }
            //console.log(this.slide.currentPos);
        },
        nextSlide: function() {
            var groupsLen = this.groupsObj.length;
            var groupItem = $('.cgroups-grid__item').width();
            var groupContentTotal = groupsLen * groupItem;
            this.slide.slideInterval = this.$refs.rcarousel.clientWidth;
            if(groupContentTotal > (this.slide.currentPos + this.slide.slideInterval)) {
                this.slide.currentPos += this.slide.slideInterval;
                $('#cgroups-grid').css({ transform: 'translateX(-'+ this.slide.currentPos +'px)' });
            }
            // Show prev button if slide position is greater than 2 grid item.
            if(this.slide.currentPos > (groupItem * 2)) {
                this.isGroupPrev = true;
            } else {
                this.isGroupPrev = false;
            }
            //console.log((groupsLen * groupItem), this.slide.currentPos);
        }
    }
}
</script>
