<template>
    <div class="vcom-navbar">
        <a class="navbar-brand" href="#">
            <img :src="asseturl+'/img/riiqo_logo_light.png'" height="30">
        </a>
        <div class="collapse navbar-collapse navbar-footer-menu" id="navbarSupportedContent">
            <!-- Left Side Of Navbar -->
            <ul class="navbar-nav navbar-nav-icons">
                <li class="client-tooltip" data-toggle="tooltip" data-placement="top" title="Dashboard">
                    <a href="#"><span class="icon-RiiqoMain-01 dashboard-module"></span></a>
                </li>
                <!-- <li class="client-tooltip" mytitle="Website">
                    <a href=""><span class="icon-RiiqoMain-02 website-module"></span></a>
                </li>
                <li class="client-tooltip" mytitle="Products">
                    <a href="#"><span class="icon-RiiqoMain-03 products-module"></span></a>
                </li> -->
                <li class="client-tooltip" mytitle="Customer">
                    <a href="#"><span class="icon-RiiqoMain-04 customer-module"></span></a>
                </li>
                <!-- <li class="client-tooltip" mytitle="HR">
                    <a href="#"><span class="icon-RiiqoMain-06 hr-module"></span></a>
                </li> -->
                <li class="client-tooltip" mytitle="Interchange">
                    <a href="#"><span class="icon-RiiqoMain-05 interchange-module"></span></a>
                </li>
                <!-- <li class="client-tooltip" mytitle="Project">
                    <a href="#"><span class="icon-RiiqoMain-107 project-module"></span></a>
                </li>
                <li class="client-tooltip" mytitle="Analytics">
                    <a href="#"><span class="icon-RiiqoMain-07 analytics-module"></span></a>
                </li> -->
                <li class="client-tooltip" mytitle="Storage">
                    <a href=""><span class="icon-RiiqoMain-08 storage-module"></span></a>
                </li>
            </ul>

            <!-- Right Side Of Navbar -->
            <ul class="navbar-nav" id="navbar-right" v-on-clickaway="hideAllMenus">

                <!-- These buttons are here so that they can be present while the rest of the menu is being loaded. And the clickable area is much larger. -->
                <!-- Also the click event is now on the li item and not the sub span, so that the whole li item is clickable, not just the span icon. -->

                <!-- user availability -->
                <li
                    class="nav-item"
                    id="mod-available"
                    v-on:click.prevent="toggleMenu('availability')">
                    <span class="icon-available">
                        <span class="inner"></span>
                    </span>

                </li>
                <UserAvailability
                    v-bind:show-menu="(activeMenu == 'availability')"/>

                <!-- chat -->
                <li
                    class="nav-item"
                    id="mod-chat"
                    v-on:click.prevent="toggleMenu('chat')">
                    <span class="icon _Inter_Chat_Filled">
                        <span class="notif-badge">2</span>
                    </span>
                </li>
                <ChatMenu v-bind:show-menu="(activeMenu == 'chat')" ref="chat"/>

                <!-- system notification -->
                <li
                    class="nav-item"
                    id="mod-notification"
                    v-on:click.prevent="toggleMenu('notifications')">
                    <span class="icon _Syst_Notification">
                        <span class="notif-badge">8</span>
                    </span>
                </li>
                <SystemNotification v-bind:show-menu="(activeMenu == 'notifications')"/>

                <!-- right menu -->
                <li
                    class="nav-item"
                    v-on:click="toggleMenu('right-menu')">
                    <span id="mod-right-bar" class="icon _Cust_Settings_Filled"></span>
                </li>
                <RightMenuSection 
                    :activeuser=activeuser 
                    v-bind:show-menu="(activeMenu == 'right-menu')" />
            </ul>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import UserAvailability from './rightmenu/UserAvailability'
import ChatMenu from './rightmenu/ChatMenu'
import SystemNotification from './rightmenu/SystemNotification'
import RightMenuSection from './rightmenu/RightMenuSection'
// import axios from 'axios'

import { directive as onClickaway } from 'vue-clickaway';

import { Helpers } from '@/helpers'

export default Vue.extend({
    directives: {
        onClickaway: onClickaway
    },
    components: {
        UserAvailability,
        ChatMenu,
        SystemNotification,
        RightMenuSection
    },
    props: {
        activeuser: Object
    },
    data() {
        return {
            activeMenu: "",
            asseturl: Helpers.getEnv('CRM_BASE_URL'),
        }
    },
    mounted() {

    },
    methods: {
        hideAllMenus () {
            console.log("hideAllMenus")
            this.activeMenu = ''
        },
        toggleMenu (newMenu) {
            console.log("toggleMenu", newMenu)
            if (this.activeMenu == newMenu) {
                // toggle the curent Menu
                this.activeMenu = ""
            } else {
                // set the new menu
                this.activeMenu = newMenu
            }
        },
    }
});
</script>