<template>
<div>
    <div class="mod-available" v-show="showMenu">
        <div class="mod-available__item">
            <h4 class="title">Phone</h4>
            <ul>
                <li>
                    <switches
                        v-model="phoneStatus.available"
                        text-enabled="Available"
                        text-disabled="Unavailable"
                        theme="bootstrap"
                        v-bind:color="(phoneStatus.available ? 'primary': 'danger')"
                        type-bold="true"
                        ></switches>

                </li>
                <li>
                    <switches
                        v-model="phoneStatus.handOff"
                        label="Hand Off"
                        theme="bootstrap"
                        v-bind:color="(phoneStatus.handOff ? 'primary': 'danger')"
                        type-bold="true"
                        ></switches>
                </li>
                <li>
                    <switches
                        v-model="phoneStatus.forwardCalls"
                        label="Forward Calls"
                        theme="bootstrap"
                        v-bind:color="(phoneStatus.forwardCalls ? 'primary': 'danger')"
                        type-bold="true"
                    ></switches>

                </li>
            </ul>
        </div>
        <div class="mod-available__item">
            <h4 class="title">Chat</h4>

            <switches
                v-model="chatStatus.available"
                theme="bootstrap"
                v-bind:color="(chatStatus.available ? 'primary': 'danger')"
                type-bold="true"
                text-enabled="Available"
                text-disabled="Unavailable">
            </switches>

        </div>
    </div>
</div>
</template>

<script>

import Switches from 'vue-switches'
import Vue from 'vue'

export default Vue.extend({
    components: {
        'switches': Switches,
    },
    directives: {
    },
    data() {
        return {
            selectedCheckbox: [],

            phoneStatus: {
                available: false,
                handOff: false,
                forwardCalls: false
            },
            chatStatus: {
                available: false
            }

        }
    },
    watch: {

    },
    computed: {
    },
    methods: {
        togglePanel () {
            this.$emit("toggle-menu","availability")
        },
        toggleCustomSwitch (val) {
            if(this.selectedCheckbox.includes(val)) {
                this.selectedCheckbox.splice(this.selectedCheckbox.findIndex(v => v === val), 1);
                this.toggleCheckboxVal(val, 0);
            } else {
                this.selectedCheckbox.push(val);
                this.toggleCheckboxVal(val, 1);
            }
        },
        toggleCheckboxVal (checkbox, val) {
            this.customCheckbox[checkbox] = val;
        },
    },
    props: {
        showMenu: {
            type: Boolean
        }
    }
});
</script>