<template>
	<div class="row">
		<!-- Left column -->
		<div class="col-sm-6 col--left">
			<section class="sxn sxn--credits">
				<h4 class="title">Credits</h4>
				<div v-show="creditsObj">
					<div class="mod-bx" 
					v-for="(cr, i) in creditsObj" 
					:key="'cr'+i">
						<div class="row">
							<div class="col">{{ cr.item }}</div>
							<div class="col col--scnd">{{ cr.charges }}</div>
							<div class="col col--axn">
								<div v-if="cr.isAddBtn">
									<span class="_Syst_AddCircle rmBtn" data-id="rsm-lineskeywords" v-on:click="loadLinesKeywords"></span>
								</div>
								<div v-else>
									<div :id="'toggleSwitch'+i" class="switch" :data-switch-toggle="'cr'+i" v-on:click="toggleSwitch(cr.id)">
										<input type="checkbox" class="switch__checkbox" :checked="selCustomCheckbox.includes(cr.id)">
										<div class="slider"></div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
			<section class="sxn">
				<h4 class="title">Dollars</h4>
				<div v-show="dollarsObj">
					<div class="mod-bx" v-for="(dol, i) in dollarsObj" :key="'do'+i">
					<div class="row">
						<div class="col">{{ dol.item }}</div>
						<div class="col col--scnd">{{ dol.charges }}</div>
						<div class="col col--axn">
							<span class="_Syst_AddCircle" v-on:click="showPurchases"></span>
						</div>
					</div>
					</div>
				</div>
			</section>
		</div>
		<!-- End Left column -->
		<!-- Right column -->
		<div class="col-sm-6 col--right">
			<section class="sxn sxn--payment-methods">
				<h4 class="title">
					Payment Methods <a href="#" data-id="rsm-history" class="rmBtn tran-history"><span>Transaction History</span></a>
				</h4>
				<div class="mod-bx mod-bx--pm">
					<h5 class="mod-bx__title">CREDIT BALANCES</h5>
					<div class="row row--balances">
						<div class="col-sm-6 col--purchase">
							<h5 class="col__title">Last Purchased</h5>
							<PurchaseChart />
						</div>
						<div class="col-sm-6 col--free">
							<h5 class="col__title">Free</h5>
							<FreeChart />
						</div>
					</div>
					<div class="row row--notes">
						<div class="col">
							<p>Free credits do not roll over from month to month.</p>
							<p>Free credits will be used before purchased credits.</p>
						</div>
					</div>
					<div class="row row--cards-header">
						<div class="col-sm-9 col--cardsname">Cards on file</div>
						<div class="col-sm-3">
							<span class="_Syst_AddCircle rmBtn" data-id="rsm-payment" v-on:click="loadPaymentInfo"></span>
						</div>
					</div>
					<div class="row row--cards-list" v-for="(cc, i) in cardsObj" :key="'cc'+i">
						<div class="col-sm-6 col--cardsname">xxxx xxxx xxxx {{ cc.last4 }}</div>
						<div class="col-sm-3">{{ cc.exp_month }} / {{ cc.exp_year }} </div>
						<div class="col-sm-3" v-if="cc.brand == 'Visa'">
							<img :src="imgpath +'/visa.jpg'" alt="visa" />
						</div>
						<div class="col-sm-3" v-else>
							{{ cc.brand }}
						</div>
					</div>
				</div>
			</section>
		</div>
		<!-- End Right column -->
	</div>
</template>

<script>
import axios from 'axios'
import $ from 'jquery'
import { Helpers } from '@/helpers'
import FreeChart from './credits/FreeChart'
import PurchaseChart from './credits/PurchaseChart'

export default {
	data () {
		return {
			selCustomCheckbox: [],
			checked: false,
			creditsObj: [
				{ id: 1, item: 'Keyword', charges: '500c / mo', isAddBtn: true },
				{ id: 2, item: 'Call Forwarding', charges: '1.5c / min', isAddBtn: false },
				{ id: 3, item: 'Voice Transcribing', charges: '5c / min', isAddBtn: false }
			],
			dollarsObj: [
				{ id: 1, item: "Local Phone Number", charges: "$1.00 / mo", isAddBtn: true },
				{ id: 2, item: "Toll Free Phone Number", charges: "$2.00 / mo", isAddBtn: true },
				{ id: 3, item: "5 Additional Users", charges: "$25.00 / mo", isAddBtn: true },
				{ id: 4, item: "1000 Credit Pack", charges: "$10.00 / mo", isAddBtn: true }
			],
			cardsObj: [],
			ccardbaseurl: Helpers.getEnv("CRM_BASE_URL") + '/api/ccard/ajax_view',
			clientid: this.$store.state.rightmenu.clientid,
			imgpath: Helpers.getEnv("CRM_BASE_URL") + '/img',
		}
	},
	mounted: function() {
		this.cardsList();
	},
	methods: {
		loadLinesKeywords: function() {
			this.$emit('setActiveRightMenu', 'interchangeExpand');
		},
		loadPaymentInfo: function() {
			this.$emit('setActiveRightMenu', 'addCardExpand');
		},
		cardsList: function() {
			axios
				.get(`${this.ccardbaseurl}/${this.clientid}`)
				.then( response => {
					// Clear previous data
					if(this.cardsObj) {
						this.cardsObj.length = 0;
					}
					this.cardsObj = response.data;
				}).catch( (err) => {
					console.log(err)
				})
		},
		created: function (){

		},
		toggleSwitch: function(id) {
			if(this.selCustomCheckbox.includes(id)) {
				this.selCustomCheckbox.splice(this.selCustomCheckbox.findIndex(v => v === id), 1);
			} else {
				this.selCustomCheckbox.push(id);
			}
		},
		showPurchases: function() {
			this.$emit('setActiveRightMenu', 'rsmPurchases');
		}
	},
	components: {
        FreeChart,
        PurchaseChart
    },
	props: {
		rightSubMenuObj: Object
	},
}
</script>