<template>
    <div id="rightmenu-nav" class="rightmenu-nav">
        <div class="row">
            <div class="col col-name text-center">{{username}}</div>
            <div class="col-sm-4 col-logout"
                v-show="!activeSubMenu">Logout</div>
        </div>
        <ul class="collapsed">
            <li :data-id="frm.id"
                :class="'rmBtn ' + frm.class"
                v-for="(frm, i) in fullRightMenu" :key="'rm'+i"
                v-on:click="rightMenuToggle(frm.id)">
                <i :class="frm.icon"></i>
                <p class="btnlabel">{{ frm.name }}</p>
            </li>
        </ul>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    props: {
        username: String,
        isRightMenu: Boolean,
        activeSubMenu: String
    },
    data() {
        return {
            fullRightMenu: [
                {
                    id: 'rsmPersonal',
                    name: 'Personal Info',
                    icon: '_Set_PersonalInfo',
                    class: 'btn-lsm-views',
                    isActive: false
                },
                {
                    id: 'rsmInterchange',
                    name: 'Interchange Settings',
                    icon: '_Inter_TwitterRetweet_Outlined',
                    class: '',
                    isActive: false
                },
                {
                    id: 'rsmPayment',
                    name: 'Payment Info',
                    icon: '_Set_PaymentInfo',
                    class: 'btn-payment-info-menu',
                    isActive: false
                },
                // Menu item history is hidden for now.
                // {
                //     id: 'rsmHistory',
                //     name: 'History',
                //     icon: '_Cust_History',
                //     class: '',
                //     isActive: false
                // },
                {
                    id: 'rsmCredits',
                    name: 'Credits',
                    icon: '_Set_Credits',
                    class: 'btn-credits-purchases-menu',
                    isActive: false
                },
                {
                    id: 'rsmUsers',
                    name: 'Users',
                    icon: '_Set_Users',
                    class: 'btn-lsm-views',
                    isActive: false
                },
                {
                    id: 'rsmPreference',
                    name: 'Preferences',
                    icon: '_Set_Preferences',
                    class: '',
                    isActive: false
                }
            ],
            activeTab: null
        }
    },
    methods: {
        rightMenuToggle (myfrm) {
            this.$emit('setActiveRightMenu', myfrm);
        }
    },
    computed: {
        checkRightSubMenuState () {
            return this.isRightSubmenu
        }
    },
    watch: {
        /* eslint-disable */
        checkRightSubMenuState (newVal, oldVal) {
            if(!newVal) {
                $('#riiqo-right-menu').removeClass('open')
            }
        }
    },
}
</script>